import { Utils } from "../utils/Utils";
import { InetAddress } from "../utils/InetAddress";

/**
 * 消息会话描述。
 */
export class Session {
    /**
     * @param {InetAddress} address 指定会话连接的地址。
     */
    constructor(address) {
        /**
         * 会话连接的地址。
         * @type {InetAddress}
         */
        this.address = address;

        /**
         * 会话 ID 。
         * @type {number}
         */
        this.id = Utils.generateSerialNumber();

        /**
         * 会话创建时的时间戳。
         * @type {number}
         */
        this.timestamp = Date.now();

        /**
         * 会话使用的密钥。
         * @type {Array}
         */
        this.secretKey = null;
    }

    /**
     * 获取会话的 ID 。
     * 
     * @returns {number} 返回会话的 ID 。
     */
    getId() {
        return this.id;
    }

    /**
     * 获取会话的连接地址。
     * 
     * @returns {InetAddress} 返回会话的连接地址。
     */
    getAddress() {
        return this.address;
    }

    /**
     * 设置会话的密钥。
     * 
     * @param {Array} key 指定新的密钥。
     */
    setSecretKey(key) {
        this.secretKey = key;
    }

    /**
     * 获取会话的密钥。
     * 
     * @returns {Array} 返回会话的密钥。
     */
    getSecretKey() {
        return this.secretKey;
    }

    /**
     * 返回会话的字符串形式描述。
     * 
     * @returns {string} 返回会话的字符串形式描述。
     */
    toString() {
        return this.address.toString();
    }
}
