import { TalkListener } from "./TalkListener";
import { Speaker } from "./Speaker";
import { Primitive } from "./Primitive";
import { TalkService } from "./TalkService";
import { TalkError } from "./TalkError";

/**
 * Talk 服务的监听器代理。
 */
export class ListenerProxy extends TalkListener {

    /**
     * @param {TalkService} service 会话服务实例。
     */
    constructor(service) {
        super();
        this.service = service;
    }

    /**
     * 当收到对端发送过来的原语时该方法被调用。
     *
     * @param {Speaker} speaker 接收到数据的会话者。
     * @param {string} cellet 发送原语的 Cellet 名。
     * @param {Primitive} primitive 接收到的原语。
     */
    onListened(speaker, cellet, primitive) {
        if (null != this.service.allEventListeners) {
            for (let i = 0, len = this.service.allEventListeners.length; i < len; ++i) {
                let listener = this.service.allEventListeners[i];
                listener.onListened(speaker, cellet, primitive);
            }
        }

        if (null != this.service.listeners) {
            let listener = this.service.listeners.get(cellet);
            if (null != listener) {
                listener.onListened(speaker, cellet, primitive);
            }
        }

        if (null != this.service.eventListeners) {
            this.triggerEvent('onListened', {
                speaker: speaker,
                cellet: cellet,
                primitive: primitive
            });
        }
    }

    /**
     * 当原语发送出去后该方法被调用。
     *
     * @param {Speaker} speaker 发送数据的会话者。
     * @param {string} cellet 接收原语的 Cellet 名。
     * @param {Primitive} primitive 发送的原语。
     */
    onSpoke(speaker, cellet, primitive) {
        if (null != this.service.allEventListeners) {
            for (let i = 0, len = this.service.allEventListeners.length; i < len; ++i) {
                let listener = this.service.allEventListeners[i];
                listener.onSpoke(speaker, cellet, primitive);
            }
        }

        if (null != this.service.listeners) {
            let listener = this.service.listeners.get(cellet);
            if (null != listener) {
                listener.onSpoke(speaker, cellet, primitive);
            }
        }

        if (null != this.service.eventListeners) {
            this.triggerEvent('onSpoke', {
                speaker: speaker,
                cellet: cellet,
                primitive: primitive
            });
        }
    }

    /**
     * 当收到服务器回送的原语应答时该方法被调用。
     *
     * @param {Speaker} speaker 发送数据的会话者。
     * @param {string} cellet 接收原语的 Cellet 名。
     * @param {Primitive} primitive 发送的原语。
     */
    onAck(speaker, cellet, primitive) {
        if (null != this.service.allEventListeners) {
            for (let i = 0, len = this.service.allEventListeners.length; i < len; ++i) {
                let listener = this.service.allEventListeners[i];
                listener.onAck(speaker, cellet, primitive);
            }
        }

        if (null != this.service.listeners) {
            let listener = this.service.listeners.get(cellet);
            if (null != listener) {
                listener.onAck(speaker, cellet, primitive);
            }
        }

        if (null != this.service.eventListeners) {
            this.triggerEvent('onAck', {
                speaker: speaker,
                cellet: cellet,
                primitive: primitive
            });
        }
    }

    /**
     * 当发送的原语在指定时间内没有应答时该方法被调用。
     *
     * @param {Speaker} speaker 发送数据的会话者。
     * @param {string} cellet 接收原语的 Cellet 名。
     * @param {Primitive} primitive 发送的原语。
     */
    onSpeakTimeout(speaker, cellet, primitive) {
        if (null != this.service.allEventListeners) {
            for (let i = 0, len = this.service.allEventListeners.length; i < len; ++i) {
                let listener = this.service.allEventListeners[i];
                listener.onSpeakTimeout(speaker, cellet, primitive);
            }
        }

        if (null != this.service.listeners) {
            let listener = this.service.listeners.get(cellet);
            if (null != listener) {
                listener.onSpeakTimeout(speaker, cellet, primitive);
            }
        }

        if (null != this.service.eventListeners) {
            this.triggerEvent('onSpeakTimeout', {
                speaker: speaker,
                cellet: cellet,
                primitive: primitive
            });
        }
    }

    /**
     * 当会话者连接到服务器完成握手后该方法被调用。
     *
     * @param {Speaker} speaker 发送数据的会话者。
     */
    onContacted(speaker) {  
        if (null != this.service.eventListeners) {
            this.triggerEvent('onContacted', {
                speaker: speaker
            });
        }

        if (null != this.service.allEventListeners) {
            for (let i = 0, len = this.service.allEventListeners.length; i < len; ++i) {
                let listener = this.service.allEventListeners[i];
                listener.onContacted(speaker);
            }
        }

        if (null != this.service.listeners) {
            let keys = this.service.listeners.keySet();
            for (let i = 0; i < keys.length; ++i) {
                let cellet = keys[i];
                let listener = this.service.listeners.get(cellet);
                let celletSpeaker = this.service.celletSpeakerMap.get(cellet);
                if (null != celletSpeaker) {
                    if (celletSpeaker == speaker) {
                        listener.onContacted(speaker);
                    }
                }
                else {
                    listener.onContacted(speaker);
                }
            }
        }
    }

    /**
     * 当会话者断开与服务器的连接时该方法被调用。
     *
     * @param {Speaker} speaker 发送数据的会话者。
     */
    onQuitted(speaker) {
        if (null != this.service.eventListeners) {
            this.triggerEvent('onQuitted', {
                speaker: speaker
            });
        }

        if (null != this.service.allEventListeners) {
            for (let i = 0, len = this.service.allEventListeners.length; i < len; ++i) {
                let listener = this.service.allEventListeners[i];
                listener.onQuitted(speaker);
            }
        }

        if (null != this.service.listeners) {
            let keys = this.service.listeners.keySet();
            for (let i = 0; i < keys.length; ++i) {
                let cellet = keys[i];
                let listener = this.service.listeners.get(cellet);
                let celletSpeaker = this.service.celletSpeakerMap.get(cellet);
                if (null != celletSpeaker) {
                    if (celletSpeaker == speaker) {
                        listener.onQuitted(speaker);
                    }
                }
                else {
                    listener.onQuitted(speaker);
                }
            }
        }
    }

    /**
     * 当发生故障时该方法被调用。
     *
     * @param {Speaker} speaker 发送数据的会话者。
     * @param {TalkError} error 故障描述。
     */
    onFailed(speaker, error) {
        if (null != this.service.allEventListeners) {
            for (let i = 0, len = this.service.allEventListeners.length; i < len; ++i) {
                let listener = this.service.allEventListeners[i];
                listener.onFailed(speaker, error);
            }
        }

        if (null != this.service.listeners) {
            let listeners = this.service.listeners.values();
            for (let i = 0; i < listeners.length; ++i) {
                listeners[i].onFailed(speaker, error);
            }
        }

        if (null != this.service.eventListeners) {
            this.triggerEvent('onFailed', {
                speaker: speaker,
                error: error
            });
        }
    }

    /**
     * 查找对应名称的回调函数并调用该函数。
     * 
     * @private
     * @param {*} name 事件名。
     * @param {*} event 事件对象。
     */
    triggerEvent(name, event) {
        let list = this.service.eventListeners.get(name);
        if (null != list) {
            for (let i = 0; i < list.length; ++i) {
                let fun = list[i];
                fun.call(null, event);
            }
        }

        // 全局事件监听
        list = this.service.eventListeners.get('*');
        if (null != list) {
            for (let i = 0; i < list.length; ++i) {
                let fun = list[i];
                fun.call(null, event);
            }
        }
    }
}
