import { MessageErrorCode } from "../net/MessageErrorCode";

/**
 * 会话通信故障描述。
 */
export class TalkError {
    /**
     * @param {number} 会话错误码。
     */
    constructor(error) {
        /**
         * 错误码。
         * @type {number}
         */
        this.errorCode = error;

        /**
         * 错误描述。
         * @type {string}
         */
        this.description = "Network error";
    }

    /**
     * 将消息层错误代码转为故障。
     *
     * @param {number} messageError 消息错误码。
     * @returns {TalkError} 返回会话错误实体。
     * @see MessageErrorCode
     */
    static transformError(messageError) {
        let error = new TalkError(1000);

        switch (messageError) {
            case MessageErrorCode.CONNECT_FAILED:
            case MessageErrorCode.CONNECT_TIMEOUT:
                error.errorCode = TalkError.NetworkConnectError;
                error.description = 'Network connection error';
                break;
            case MessageErrorCode.SOCKET_FAILED:
            case MessageErrorCode.BIND_FAILED:
            case MessageErrorCode.LISTEN_FAILED:
            case MessageErrorCode.ACCEPT_FAILED:
                error.errorCode = TalkError.NetworkSocketError;
                error.description = 'Socket error';
                break;
            case MessageErrorCode.WRITE_TIMEOUT:
            case MessageErrorCode.READ_TIMEOUT:
            case MessageErrorCode.WRITE_FAILED:
            case MessageErrorCode.READ_FAILED:
            case MessageErrorCode.WRITE_OUTOFBOUNDS:
                error.errorCode = TalkError.NetworkIOError;
                error.description = 'Socket I/O error';
                break;
            default:
                break;
        }
        return error;
    }

    /**
     * 获取错误码。
     *
     * @returns {number} 返回错误码。
     */
    getErrorCode() {
        return this.errorCode;
    }

    /**
     * 获取错误文本描述。
     * 
     * @returns {string} 返回错误文本描述。
     */
    getDescription() {
        return this.description;
    }
}

/** 网络一般错误。 */
TalkError.NetworkError = 1000;
/** 网络链路错误。 */
TalkError.NetworkConnectError = 1010;
/** 网络 Socket 错误。 */
TalkError.NetworkSocketError = 1020;
/** 网络 I/O 错误。 */
TalkError.NetworkIOError = 1030;
/** 心跳超时。 */
TalkError.HeartbeatTimeout = 1040;
/** 握手错误。 */
TalkError.HandshakeError = 2000;
/** 握手超时。 */
TalkError.HandshakeTimeout = 2001;
