import { LiteralBase } from "./LiteralBase";
import { ByteUtils } from "../utils/ByteUtils";

/**
 * 原语素材。用于封装可被识别字面义的数据。
 */
export class Stuff {

    /**
     * @param {*} value 指定数据值。
     * @param {LiteralBase} [literal] 指定数据的字面义。
     */
    constructor(value, literal) {
        if (value === undefined && literal === undefined) {
            return;
        }

        if (literal !== undefined && literal != null) {
            // 明确字面义进行解析
            this.literal = literal;
            switch (literal) {
                case LiteralBase.STRING:
                    this.value = ByteUtils.stringToBytes(value);
                    break;
                case LiteralBase.INT:
                    this.value = ByteUtils.intToBytes(value);
                    break;
                case LiteralBase.LONG:
                    this.value = ByteUtils.longToBytes(value);
                    break;
                case LiteralBase.JSON:
                    this.value = ByteUtils.stringToBytes(JSON.stringify(value));
                    break;
                case LiteralBase.BOOL:
                    this.value = ByteUtils.boolToBytes(value);
                    break;
                case LiteralBase.FLOAT:
                    this.value = ByteUtils.floatToBytes(value);
                    break;
                case LiteralBase.DOUBLE:
                    this.value = ByteUtils.doubleToBytes(value);
                    break;
                case LiteralBase.BIN:
                    this.value = value;
                    break;
                default:
                    break;
            }
        }
        else {
            // 猜测字面义进行解析
            let vt = (typeof value);

            if (vt == 'string') {
                this.value = ByteUtils.stringToBytes(value);
                this.literal = LiteralBase.STRING;
            }
            else if (vt == 'object') {
                this.literal = LiteralBase.JSON;
                this.value = ByteUtils.stringToBytes(JSON.stringify(value));
            }
            else if (vt == 'number') {
                let reg = /^-?\d+$/;
                let doubleReg = /^[-\+]?\d+(\.\d+)?$/;
                if (reg.test(value.toString())) {
                    if (value >= -2147483648 && value <= 2147483647) {
                        this.literal = LiteralBase.INT;
                        this.value = ByteUtils.intToBytes(value);
                    }
                    else {
                        this.literal = LiteralBase.LONG;
                        this.value = ByteUtils.longToBytes(value);
                    }
                }
                else if (doubleReg.test(value.toString())) {
                    this.literal = LiteralBase.DOUBLE;
                    this.value = ByteUtils.doubleToBytes(value);
                }
                else {
                    this.literal = LiteralBase.FLOAT;
                    this.value = ByteUtils.floatToBytes(value);
                }
            }
            else if (vt == 'boolean') {
                this.literal = LiteralBase.BOOL;
                this.value = ByteUtils.boolToBytes(value);
            }
            else {
                this.literal = LiteralBase.BIN;
                this.value = value;
            }
        }
    }

    /**
     * 重置数据。
     * 
     * @private
     * @param {Array} array 数据字节数组。
     * @param {LiteralBase} literal 数据字面义。
     */
    restore(array, literal) {
        this.value = array;
        this.literal = literal;
    }

    /**
     * 获取数据的字面义类型。
     * 
     * @returns 返回数据的字面义类型。
     */
    getLiteralBase() {
        return this.literal;
    }

    /**
     * 获取字节数组封装的数据。
     * 
     * @returns {Array} 返回字节数组封装的数据。
     */
    getValue() {
        return this.value;
    }

    /**
     * 获取与字面义一致的数据值。例如，当字面义是 {@link LiteralBase.STRING} 时，返回的数据类型就是 {@linkcode 'string'} 。
     * 
     * @returns {*} 返回字面义的值。
     */
    getLiteralValue() {
        return this.getValueAs(this.literal);
    }

    /**
     * 获取指定字面义的数据。
     * 
     * @param {LiteralBase} literal 指定字面义。
     * @returns {*} 返回指定字面义的数据。
     */
    getValueAs(literal) {
        switch (literal) {
            case LiteralBase.STRING:
                return this.getValueAsString();
            case LiteralBase.INT:
                return this.getValueAsInt();
            case LiteralBase.LONG:
                return this.getValueAsLong();
            case LiteralBase.JSON:
                return this.getValueAsJson();
            case LiteralBase.BOOL:
                return this.getValueAsBool();
            case LiteralBase.FLOAT:
                return this.getValueAsFloat();
            case LiteralBase.DOUBLE:
                return this.getValueAsDouble();
            case LiteralBase.BIN:
                return this.getValue();
            default:
                return this.value;
        }
    }

    /**
     * 获取字符串形式的数据值。
     * 
     * @returns {string} 返回字符串形式的数据值。
     */
    getValueAsString() {
        return ByteUtils.toString(this.value);
    }

    /**
     * 获取整数形式的数据值。
     *
     * @returns {number} 返回整数形式的数据值。
     */
    getValueAsInt() {
        return ByteUtils.toInt(this.value);
    }

    /**
     * 获取长整数形式的数据值。
     *
     * @returns {number} 返回长整数形式的数据值。
     */
    getValueAsLong() {
        return ByteUtils.toLong(this.value);
    }

    /**
     * 获取浮点数形式的数据值。
     *
     * @returns {number} 返回浮点数形式的数据值。
     */
    getValueAsFloat() {
        return ByteUtils.toFloat(this.value);
    }

    /**
     * 获取双精浮点形式的数据值。
     *
     * @returns {number} 返回双精浮点形式的数据值。
     */
    getValueAsDouble() {
        return ByteUtils.toDouble(this.value);
    }

    /**
     * 获取布尔型形式的数据值。
     *
     * @returns {boolean} 返回布尔型形式的数据值。
     */
    getValueAsBool() {
        return ByteUtils.toBool(this.value);
    }

    /**
     * 获取 JSON 形式的数据值。
     *
     * @returns {object} 返回 JSON 形式的数据值。
     */
    getValueAsJson() {
        let jsonstr = ByteUtils.toString(this.value);
        return JSON.parse(jsonstr);
    }

    /**
     * 判断两个 Stuff 的数据是否相同。
     * 
     * @param {Stuff} other 待比较的另一个 Stuff 。
     * @returns {boolean} 如果数据值相同，返回 {@linkcode true} 。
     */
    equals(other) {
        if (null == other || !(other instanceof Stuff)) {
            return false;
        }

        if (other.literal != this.literal || other.value.length != this.value.length) {
            return false;
        }

        for (let i = 0, size = other.value.length; i < size; ++i) {
            if (other.value[i] != this.value[i]) {
                return false;
            }
        }

        return true;
    }
}
