import { ByteUtils } from "../utils/ByteUtils";
import { Primitive } from "./Primitive";
import { Stuff } from "./Stuff";

/**
 * Talk 协议描述。
 *
 * 数据包字段，单位 byte：
 * <pre class="prettyprint">
 * <code>
 * +--|-00-|-01-|-02-|-03-|-04-|-05-|-06-|-07-+<br>
 * |--+---------------------------------------+<br>
 * |01| VER| SN |         RES       |    PN   |<br>
 * |--+---------------------------------------+<br>
 * |02|              DATA BEGIN               |<br>
 * |--+---------------------------------------+<br>
 * |03|                ... ...                |<br>
 * |--+---------------------------------------+<br>
 * |04|               DATA END                |<br>
 * |--+---------------------------------------+<br>
 * </code>
 * </pre>
 * 
 * DATA 段的数据使用 0x1E 作为数据分隔符。
 * 
 * @interface
 * @property {Array} Handshake 握手协议。
 * @property {Array} SpeakNoAck 无应答会话协议。
 * @property {Array} SpeakAck 可应答会话协议。
 * @property {Array} Stream 流会话协议。
 * @property {Array} StreamControl 流会话控制协议。
 * @property {Array} Ack 会话应答协议。
 * @property {Array} Heartbeat 心跳协议。
 * @property {Array} HeartbeatAck 心跳应答协议。
 * @property {Array} Goodbye 结束会话协议。
 */
export const Protocol = {

    Handshake: [ 72, 83 ],      // [ 'H', 'S' ] 握手协议。

    SpeakNoAck: [ 83, 78 ],     // [ 'S', 'N' ] 无应答会话协议。

    SpeakAck: [ 83, 65 ],       // [ 'S', 'A' ] 可应答会话协议。

    Stream: [ 83, 84 ],         // [ 'S', 'T' ] 流会话协议。

    StreamControl: [ 83, 67 ],  // [ 'S', 'C' ] 流会话控制协议。

    Ack: [ 65, 67 ],            // [ 'A', 'C' ] 会话应答协议。

    Heartbeat: [ 72, 66 ],      // [ 'H', 'B' ] 心跳协议。

    HeartbeatAck: [ 72, 65 ],   // [ 'H', 'A' ] 心跳应答协议。

    Goodbye: [ 71, 66 ],        // [ 'G', 'B' ] 结束会话协议。

    Version: 0x01, // 协议默认版本。

    Reserve: [ 0x11, 0x24, 0x11, 0x24 ],  // 协议默认保留字。

    SEPARATE_CHAR: 0x1E, // 协议数据分隔符。

    ESCAPE_CHAR: 0x5C, // 协议数据转义符 '\\'。

    sGlobalSN: 0,   // 全局 SN 记录。

    /**
     * 识别协议类型。
     * 
     * @param {Array} data 封装了原始报文的数组缓存。
     * @returns {Array} 返回对应协议。
     */
    recognize: function(data) {
        if (data.length < 10) {
            return null;
        }

        let b6 = parseInt(data[6]);
        let b7 = parseInt(data[7]);

        if (b6 == Protocol.SpeakNoAck[0] && b7 == Protocol.SpeakNoAck[1]) {
            return Protocol.SpeakNoAck;
        }
        else if (b6 == Protocol.SpeakAck[0] && b7 == Protocol.SpeakAck[1]) {
            return Protocol.SpeakAck;
        }
        else if (b6 == Protocol.Stream[0] && b7 == Protocol.Stream[1]) {
            return Protocol.Stream;
        }
        else if (b6 == Protocol.StreamControl[0] && b7 == Protocol.StreamControl[1]) {
            return Protocol.StreamControl;
        }
        else if (b6 == Protocol.Ack[0] && b7 == Protocol.Ack[1]) {
            return Protocol.Ack;
        }
        else if (b6 == Protocol.Heartbeat[0] && b7 == Protocol.Heartbeat[1]) {
            return Protocol.Heartbeat;
        }
        else if (b6 == Protocol.HeartbeatAck[0] && b7 == Protocol.HeartbeatAck[1]) {
            return Protocol.HeartbeatAck;
        }
        else if (b6 == Protocol.Handshake[0] && b7 == Protocol.Handshake[1]) {
            return Protocol.Handshake;
        }
        else {
            return null;
        }
    },

    /**
     * 序列化握手协议。
     *
     * @param {Array} key 指定通信密钥。
     * @param {string} tag 指定内核标签。
     * @returns {Array} 返回协议的字节编码组。
     */
    serializeHandshake: function(key, tag) {
        let buf = new Array();
        // 版本 - 1
        buf.push(Protocol.Version);
        // 序列号 - 1
        buf.push(Protocol.consumeSN());
        // 保留段 - 4
        Array.prototype.push.apply(buf, Protocol.Reserve);
        // 协议名 - 2
        Array.prototype.push.apply(buf, Protocol.Handshake);

        // 访问 Key
        Array.prototype.push.apply(buf, key);

        // 标签
        buf.push(Protocol.SEPARATE_CHAR);
        Array.prototype.push.apply(buf, ByteUtils.stringToBytes(tag));
        return buf;
    },

    /**
     * 反序列化握手协议。
     *
     * @param {Array} data 原始协议的字节编码组。
     * @returns {object} 返回协议对象。
     */
    deserializeHandshake: function(data) {
        let prtl = { key: null, tag: null };

        let buf = new Array();
        let index = 0;
        for (let i = 8; i < data.length; ++i) {
            do {
                let b = data[i];
                if (b == Protocol.SEPARATE_CHAR) {
                    break;
                }

                // buf.push(ByteUtils.toString(b));
                buf.push(String.fromCharCode(b));
                ++i;
            } while (i < data.length);

            if (index == 0) {
                prtl.key = new Array();
                Array.prototype.push.apply(prtl.key, buf);
            }
            else if (index == 1) {
                prtl.tag = buf.join('');
            }

            buf.splice(0, buf.length);
            ++index;
        }

        return prtl;
    },

    /**
     * 序列化会话协议。
     *
     * 每段数据的格式：
     * 数据字面义(1字节)+数据内容(变长)+数据分隔符(1字节)...(依次循环)
     *
     * @param {string} target 指定目标 Cellet 名称。
     * @param {Primitive} primitive 指定封装了数据的原语。
     * @param {boolean} ack 是否需要对端进行接收应答。
     * @returns {Array} 返回协议的字节编码组。
     */
    serializeSpeak: function(target, primitive, ack) {
        let buf = new Array();
        // 版本 - 1
        buf.push(Protocol.Version);
        // 序列号 - 1
        buf.push(Protocol.consumeSN());
        // 保留段 - 4，使用 SN 填充
        Array.prototype.push.apply(buf, primitive.getSN());
        // 协议名 - 2
        if (ack) {
            Array.prototype.push.apply(buf, Protocol.SpeakAck);
        }
        else {
            Array.prototype.push.apply(buf, Protocol.SpeakNoAck);
        }

        // 模块名
        Array.prototype.push.apply(buf, ByteUtils.stringToBytes(target));

        // 标签
        buf.push(Protocol.SEPARATE_CHAR);

        // 原语数据
        for (let i = 0, sl = primitive.stuffList; i < sl.length; ++i) {
            // 字面义
            buf.push(sl[i].literal);

            // 数据
            for (let j = 0, val = sl[i].value; j < val.length; ++j) {
                if (val[j] == Protocol.SEPARATE_CHAR) {
                    // 转义
                    buf.push(Protocol.ESCAPE_CHAR);
                }
                else if (val[j] == Protocol.ESCAPE_CHAR) {
                    // 转义
                    buf.push(Protocol.ESCAPE_CHAR);
                }
                buf.push(val[j]);
            }
            // 分隔数据
            buf.push(Protocol.SEPARATE_CHAR);
        }
        return buf;
    },

    /**
     * 反序列化会话协议。
     *
     * @param {Array} bytes 协议的字节编码组。
     * @returns {object} 返回协议对象。
     */
    deserializeSpeak: function(bytes) {
        if (bytes.length < 10) {
            return null;
        }

        let prtl = Protocol.recognize(bytes);

        // 提取 SN
        let sn = bytes.slice(2, 6);

        let protocol = { target: null, primitive: null, ack: (prtl == Protocol.SpeakAck) };

        let buf = new Array();
        // 目标
        for (let i = 8; i < bytes.length; ++i) {
            let b = bytes[i];

            if (b == Protocol.SEPARATE_CHAR) {
                break;
            }

            // buf.push(ByteUtils.toString(b));
            buf.push(String.fromCharCode(b));
        }

        // 赋值
        protocol.target = buf.join('');

        // 调整游标位置
        let start = 8 + buf.length + 1;

        let primitive = new Primitive(sn);

        for (let i = start; i < bytes.length; ++i) {
            // 字面义
            let lb = bytes[i];

            let cbuf = [];

            // 定位到数据内容位置
            ++i;

            // 数据内容提取
            while (i < bytes.length) {
                let dc = bytes[i];

                if (dc == Protocol.ESCAPE_CHAR) {
                    ++i;
                    dc = bytes[i];
                    cbuf.push(dc);
                    // 下一字节
                    ++i;
                    continue;
                }
                else if (dc == Protocol.SEPARATE_CHAR) {
                    // 结束
                    break;
                }

                cbuf.push(dc);
                // 下一字节
                ++i;
            }

            let stuff = new Stuff();
            stuff.restore(cbuf, lb);
            primitive.commit(stuff);
        }

        // 赋值
        protocol.primitive = primitive;

        return protocol;
    },

    /**
     * 序列化会话应答协议。
     *
     * @param {string} target 指定目标 Cellet 名称。
     * @param {Primitive} primitive 指定要应答的源原语。
     * @returns {Array} 返回协议的字节编码组。
     */
    serializeAck: function(target, primitive) {
        let buf = new Array();
        // 版本 - 1
        buf.push(Protocol.Version);
        // 序列号 - 1
        buf.push(Protocol.consumeSN());
        // 保留段 - 4，使用 SN 填充
        Array.prototype.push.apply(buf, primitive.getSN());
        // 协议名 - 2
        Array.prototype.push.apply(buf, Protocol.Ack);

        // 模块名
        Array.prototype.push.apply(buf, ByteUtils.stringToBytes(target));
        return buf;
    },

    /**
     * 反序列化会话应答协议。
     *
     * @param {Array} bytes 协议的字节编码组。
     * @returns {object} 返回协议对象。
     */
    deserializeAck: function(bytes) {
        if (bytes.length < 8) {
            return null;
        }

        let protocol = { target: null, sn: null };

        // 提取 SN
        protocol.sn = bytes.slice(2, 6);

        // Target
        protocol.target = ByteUtils.toString(bytes.slice(8, bytes.length));

        return protocol;
    },

    /**
     * 序列化心跳协议。
     *
     * @param {string} tag 当前会话的内核标签。
     * @param {number} timestamp 心跳时间戳。
     * @returns {Array} 返回协议的字节编码组。
     */
    serializeHeartbeat: function(tag, timestamp) {
        let result = new Array();
        // 版本 - 1
        result.push(Protocol.Version);
        // 序列号 - 1
        result.push(Protocol.consumeSN());
        // 保留段 - 4
        Array.prototype.push.apply(result, Protocol.Reserve);
        // 协议名 - 2
        Array.prototype.push.apply(result, Protocol.Heartbeat);

        // 标签
        Array.prototype.push.apply(result, ByteUtils.toBytes(tag));

        // 时间戳
        result.push(Protocol.SEPARATE_CHAR);
        Array.prototype.push.apply(result, ByteUtils.longToBytes(timestamp));

        return result;
    },

    /**
     * 反序列化心跳协议。
     *
     * @param {Array} bytes 协议的字节编码组。
     * @returns {object} 返回协议对象。
     */
    deserializeHeartbeat: function(bytes) {
        let prtl = {
            tag: null,
            originateTimestamp: 0,
            receiveTimestamp: 0,
            transmitTimestamp: 0
        };

        let index = 0;
        for (let i = 8; i < bytes.length; ++i) {
            let buf = new Array();
            do {
                let b = bytes[i];
                if (b == Protocol.SEPARATE_CHAR) {
                    break;
                }
                else if (b == Protocol.ESCAPE_CHAR) {
                    // 解析转义
                    i += 1;
                    b = bytes[i];
                }

                buf.push(b);
                ++i;
            } while (i < bytes.length);

            if (index == 0) {
                prtl.tag = ByteUtils.toString(buf);
            }
            else if (index == 1) {
                prtl.originateTimestamp = ByteUtils.toLong(buf);
            }

            buf.splice(0, buf.length);
            ++index;
        }

        return prtl;
    },

    /**
     * 序列化心跳应答协议。
     *
     * @param {string} tag 内核标签。
     * @param {number} originateTimestamp 应答心跳的原时间戳。
     * @param {number} receiveTimestamp 接收到心跳时的时间戳。
     * @param {number} transmitTimestamp 发送该应答时的时间戳。
     * @returns {Array} 返回协议的字节编码组。
     */
    serializeHeartbeatAck: function(tag, originateTimestamp, receiveTimestamp, transmitTimestamp) {
        let result = new Array();
        // 版本 - 1
        result.push(Protocol.Version);
        // 序列号 - 1
        result.push(Protocol.consumeSN());
        // 保留段 - 4
        Array.prototype.push.apply(result, Protocol.Reserve);
        // 协议名 - 2
        Array.prototype.push.apply(result, Protocol.HeartbeatAck);

        // 标签
        Array.prototype.push.apply(result, ByteUtils.toBytes(tag));

        // 时间戳
        result.push(Protocol.SEPARATE_CHAR);
        Array.prototype.push.apply(result, Protocol.escape(ByteUtils.longToBytes(originateTimestamp)));

        result.push(Protocol.SEPARATE_CHAR);
        Array.prototype.push.apply(result, Protocol.escape(ByteUtils.longToBytes(receiveTimestamp)));

        result.push(Protocol.SEPARATE_CHAR);
        Array.prototype.push.apply(result, Protocol.escape(ByteUtils.longToBytes(transmitTimestamp)));

        return result;
    },

    /**
     * 反序列化心跳应答协议。
     *
     * @param {Array} bytes 协议的字节编码组。
     * @returns {object} 返回协议对象。
     */
    deserializeHeartbeatAck: function(bytes) {
        let hp = {
            tag: null,
            originateTimestamp: 0,
            receiveTimestamp: 0,
            transmitTimestamp: 0
        };

        let index = 0;
        for (let i = 8; i < bytes.length; ++i) {
            let buf = new Array();
            do {
                let b = bytes[i];
                if (b == Protocol.SEPARATE_CHAR) {
                    break;
                }
                else if (b == Protocol.ESCAPE_CHAR) {
                    // 解析转义
                    i += 1;
                    b = bytes[i];
                }

                buf.push(b);
                ++i;
            } while (i < bytes.length);

            if (index == 0) {
                hp.tag = ByteUtils.toString(buf);
            }
            else if (index == 1) {
                let originateTimestamp = buf.slice(0, 8);
                hp.originateTimestamp = ByteUtils.toLong(originateTimestamp);
            }
            else if (index == 2) {
                let receiveTimestamp = buf.slice(0, 8);
                hp.receiveTimestamp = ByteUtils.toLong(receiveTimestamp);
            }
            else if (index == 3) {
                let transmitTimestamp = buf.slice(0, 8);
                hp.transmitTimestamp = ByteUtils.toLong(transmitTimestamp);
            }

            buf.splice(0, buf.length);
            ++index;
        }

        return hp;
    },

    /**
     * 序列化 Goodbye 协议。
     *
     * @returns {Array} 返回协议的字节编码组。
     */
    serializeGoodbye: function() {
        let result = new Array();
        // 版本 - 1
        result.push(Protocol.Version);
        // 序列号 - 1
        result.push(Protocol.consumeSN());
        // 保留段 - 4
        Array.prototype.push.apply(result, Protocol.Reserve);
        // 协议名 - 2
        Array.prototype.push.apply(result, Protocol.Goodbye);

        return result;
    },

    /**
     * 返回可用的包序号。
     *
     * @returns {number} 按照正整数自增规则生成 0 ~ 127 的序号。
     */
    consumeSN: function() {
        let sn = Protocol.sGlobalSN;
        Protocol.sGlobalSN += 1;
        if (Protocol.sGlobalSN > 127) {
            Protocol.sGlobalSN = 0;
        }
        return sn;
    },

    /**
     * 对字节数组内的数据进行转义处理。
     * 
     * @private
     * @param {Array} input 输入数据。
     * @returns {Array} 返回完成转义的数据。
     */
    escape: function(input) {
        let size = 0;
        for (let i = 0; i < input.length; ++i) {
            ++size;
            if (input[i] == Protocol.SEPARATE_CHAR || input[i] == Protocol.ESCAPE_CHAR) {
                // 需要转义
                ++size;
            }
        }

        // 长度相同，没有转义
        if (size == input.length) {
            return input;
        }

        let output = new Array(size);
        for (let i = 0, o = 0; i < input.length; ++i, ++o) {
            let b = input[i];
            if (b == Protocol.SEPARATE_CHAR) {
                output[o] = Protocol.ESCAPE_CHAR;
                ++o;
            }
            else if (b == Protocol.ESCAPE_CHAR) {
                output[o] = Protocol.ESCAPE_CHAR;
                ++o;
            }
            output[o] = b;
        }

        return output;
    }
}
