/**
 * 网络地址描述类。
 */
export class InetAddress {

    /**
     * 构造函数。
     * @param {string} host 指定主机地址。
     * @param {number} port 指定端口号。
     */
    constructor(host, port) {
        /**
         * 主机地址。
         * @type {string}
         */
        this.host = host;

        /**
         * 端口号。
         * @type {number}
         */
        this.port = port;
    }

    /**
     * 返回主机地址。
     * 
     * @returns {string} 返回主机地址。
     */
    getHost() {
        return this.host;
    }

    /**
     * 返回端口号。
     * 
     * @returns {number} 返回端口号。
     */
    getPort() {
        return this.port;
    }

    /**
     * 转字符串信息。
     * 
     * @returns {string} 返回字符串形式的描述。
     */
    toString() {
        return this.host + ":" + this.port;
    }
}
