/**
 * 字节操作辅助函数。
 * 
 * @class
 */
export const ByteUtils = {

    /**
     * 根据值类型判断转字节数组类型。
     * 
     * @param {*} data 指定数据对象。
     * @returns {Array} 返回字节数组。
     */
    toBytes: function(data) {
        if (data !== undefined) {
            let vt = (typeof data);
            if (vt == 'string') {
                return this.stringToBytes(data)
            }
            else if (vt == 'number') {
                let reg = /^-?\d+$/;
                let doubleReg = /^[-\+]?\d+(\.\d+)?$/;
                if (reg.test(data.toString())) {
                    if (data > -2147483648 && data < 2147483647) {
                        return this.intToBytes(data)
                    } else {
                        return this.longToBytes(data)
                    }
                }
                else if (doubleReg.test(data.toString())) {
                    return this.doubleToBytes(data);
                }
                else {
                    return this.floatToBytes(data);
                }
            }
            else if (vt == 'boolean') {
                return this.boolToBytes(data)
            }
            else if (vt == 'object') {
                return this.stringToBytes(JSON.stringify(data));
            }
        }
    },

    /**
     * 整数转字节数组。
     *
     * @param {number} data 整数类型数据。
     * @returns {Array} 返回字节数组。
     */
    intToBytes: function(data) {
        let bytes = [];
        bytes[0] = this.over127((data & 0xff));
        bytes[1] = this.over127(((data & 0xff00) >> 8));
        bytes[2] = this.over127(((data & 0xff0000) >> 16));
        bytes[3] = this.over127(((data & 0xff000000) >> 24));
        return bytes;
    },

    /**
     * 长整型转字节数组。
     *
     * @param {number} data 长整数类型数据。
     * @returns {Array} 返回字节数组。
     */
    longToBytes: function(data) {
        // let bytes = [];
        // bytes[0] = this.over127(data & 0xff);
        // bytes[1] = this.over127((data >> 8) & 0xff);
        // bytes[2] = this.over127((data >> 16) & 0xff);
        // bytes[3] = this.over127((data >> 24) & 0xff);
        // bytes[4] = this.over127((data >> 32) & 0xff);
        // bytes[5] = this.over127((data >> 40) & 0xff);
        // bytes[6] = this.over127((data >> 48) & 0xff);
        // bytes[7] = this.over127((data >> 56) & 0xff);
        // return bytes;
        let src = [];
        src[0] = (data & 0xff);
        src[1] = (this.toRight(data, 8) & 0xff);
        src[2] = (this.toRight(data, 16) & 0xff);
        src[3] = (this.toRight(data, 24) & 0xff);
        src[4] = (this.toRight(data, 32) & 0xff);
        src[5] = (this.toRight(data, 40) & 0xff);
        src[6] = (this.toRight(data, 48) & 0xff);
        src[7] = (this.toRight(data, 56) & 0xff);
        return src;
    },

    /**
     * 浮点型转字节数组。
     *
     * @param {number} data 浮点数类型数据。
     * @returns {Array} 返回字节数组。
     */
    floatToBytes: function(data) {
        let str = data.toString();
        let bytes = [];
        for (let i = 0; i < str.length; ++i) {
            bytes.push(str.charCodeAt(i));
        }
        return bytes;
    },

    /**
     * 双精浮点型转字节数组。
     *
     * @param {number} data 双精浮点数类型数据。
     * @returns {Array} 返回字节数组。
     */
    doubleToBytes: function(data) {
        let str = data.toString();
        let bytes = [];
        for (let i = 0; i < str.length; ++i) {
            bytes.push(str.charCodeAt(i));
        }
        return bytes;
    },

    /**
     * 字符串转字节数组。
     *
     * @param {string} data 字符串类型数据。
     * @returns {Array} 返回字节数组。
     */
    stringToBytes: function(data) {
        return (new TextEncoder('utf-8')).encode(data);
    },

    /**
     * 布尔型转字节数组。
     *
     * @param {boolean} data 布尔型数据。
     * @returns {Array} 返回字节数组。
     */
    boolToBytes: function(data) {
        return [ ( data ? 1 : 0 ) ];
    },

    /**
     * 将字符串的每个字符转换到数组。
     * 
     * @param {string} str 字符串数据。
     * @returns {Array} 返回字节数组。
     */
    toArray: function(str) {
        let ret = new Array();
        for (let i = 0, len = str.length; i < len; ++i) {
            ret.push(str.charAt(i));
        }
        return ret;
    },

    /**
     * 字节数组转整数。
     *
     * @param {Array|Uint8Array} bytes 字节数组。
     * @returns {number} 返回整数型。
     */
    toInt: function(bytes) {
        return (0xff & bytes[0])
            | (0xff00 & (bytes[1] << 8))
            | (0xff0000 & (bytes[2] << 16))
            | (0xff000000 & (bytes[3] << 24));
    },

    /**
     * 字节数组转长整型。
     *
     * @param {Array|Uint8Array} bytes 字节数组。
     * @returns {number} 返回长整型。
     */
    toLong: function(bytes) {
        // return (0xff & bytes[0])
		// 		| (0xff00 & (bytes[1] << 8))
		// 		| (0xff0000 & (bytes[2] << 16))
		// 		| (0xff000000 & (bytes[3] << 24))
		// 		| (0xff00000000 & (bytes[4] << 32))
		// 		| (0xff0000000000 & (bytes[5] << 40))
		// 		| (0xff000000000000 & (bytes[6] << 48))
		// 		| (0xff00000000000000 & (bytes[7] << 56));
        let low = bytes[0] |
            bytes[1] << 8 |
            bytes[2] << 16 |
            bytes[3] << 24;
        let high = bytes[4] |
            bytes[5] << 8 |
            bytes[6] << 16 |
            bytes[7] << 24;
        return ((high >>> 0) * ((1 << 16) * (1 << 16))) + (low >>> 0);
    },

    /**
     * 字节数组转浮点型。
     * 
     * @param {Array|Uint8Array} bytes 字节数组。
     * @returns {number} 返回浮点型。
     */
    toFloat: function(bytes) {
        let array = [];
        for (let i = 0; i < bytes.length; ++i) {
            array.push(String.fromCharCode(bytes[i]));
        }
        let str = array.join('');
        return parseFloat(str);
    },

    /**
     * 字节数组转双精浮点型。
     * 
     * @param {Array|Uint8Array} bytes 字节数组。
     * @returns {number} 返回浮点型。
     */
    toDouble: function(bytes) {
        let array = [];
        for (let i = 0; i < bytes.length; ++i) {
            array.push(String.fromCharCode(bytes[i]));
        }
        let str = array.join('');
        return parseFloat(str);
    },

    /**
     * 字节数组转布尔型。
     *
     * @param {Array|Uint8Array} bytes 字节数组。
     * @returns {boolean} 返回布尔型。
     */
    toBool: function(bytes) {
        return (bytes[0] == 1) ? true : false;
    },

    /**
     * 字节数组转字符串。
     *
     * @param {Array|Uint8Array} bytes 字节数组。
     * @returns {string} 返回字符串。
     */
    toString: function(bytes) {
        return new TextDecoder('utf-8').decode(new Uint8Array((typeof bytes == 'object') ? bytes : [bytes]));
    },

    /**
     * 64位-位移运算符(右) 。
     *
     * @private
     * @param {*} val 十进制
     * @param {*} bit 位移位数
     * @returns {number} 位移结果。
     */
    toRight: function(val, bit) {
        try {
            return parseInt((Array(bit + 1).join("0") + (bit > val.toString(2).length ? 0 : val.toString(2).slice(0, val.toString(2).length - bit))), 2);
        } catch (e) {
            console.log(val, bit, e);
        }
    },

    /**
     * 取补码。
     *
     * @private
     * @param {number} v 值。
     * @returns {number} 补码。
     */
    over127: function(v) {
        return v > 127 ? v - 256 : v;
    },

    /**
     * @private
     * @param {number} f 
     */
    floatToIntBits: function(f) {
        let NAN_BITS = 0|0x7FC00000;
        let INF_BITS = 0|0x7F800000;
        let ZERO_BITS = 0|0x00000000;
        let SIGN_MASK = 0|0x80000000;
        let EXP_MASK = 0|0x7F800000;
        let MANT_MASK = 0|0x007FFFFF;
        let MANT_MAX = Math.pow(2.0, 23) - 1.0;
    
        if (f != f)
            return NAN_BITS;
        let hasSign = f < 0.0 || (f == 0.0 && 1.0 / f < 0);
        let signBits = hasSign ? SIGN_MASK : 0;
        let fabs = Math.abs(f);
    
        if (fabs == Number.POSITIVE_INFINITY)
            return signBits | INF_BITS;
    
        let exp = 0, x = fabs;
        while (x >= 2.0 && exp <= 127) {
            exp++;
            x /= 2.0;
        }
        while (x < 1.0 && exp >= -126) {
            exp--;
            x *= 2.0;
        }
        // assert(x * Math.pow(2.0, exp) == fabs, "fabs");
        let biasedExp = exp + 127;
        // assert(0 <= biasedExp && biasedExp <= 254, biasedExp);
    
        if (biasedExp == 255)
            return signBit | INF_BITS;
        if (biasedExp == 0) {
            // assert(0.0 <= x && x < 2.0, "x in [0.0, 1.0)", x);
            let mantissa = x * Math.pow(2.0, 23) / 2.0;
        } else {
            // assert(1.0 <= x && x < 2.0, "x in [0.5; 1.0)", x);
            let mantissa = x * Math.pow(2.0, 23) - Math.pow(2.0, 23);
        }
        // assert(0.0 <= mantissa && mantissa <= MANT_MAX, "mantissa in [0.0, 2^23)", mantissa);
    
        //console.log("number", f, "x", x, "biasedExp", biasedExp, "mantissa", mantissa.toString(16));
        let expBits = (biasedExp << 23) & EXP_MASK;
        let mantissaBits = mantissa & MANT_MASK;
    
        //console.log("number", f, "sign", signBits.toString(16), "expBits", expBits.toString(16), "mantissaBits", mantissaBits.toString(16));
        return signBits | expBits | mantissaBits;
    }
}
