/**
 * 模拟线程。
 */
export class Thread {

    /**
     * 构造函数。
     * @param {function} runnable 指定执行入口。
     * @param {object} scope 指定作用域。
     */
    constructor(runnable, scope) {
        this.timer = 0;
        this.runnable = runnable;

        if (undefined === scope) {
            this.scope = null;
        }
        else {
            this.scope = scope;
        }
    }

    /**
     * 启动异步执行。
     */
    start() {
        let self = this;
        this.timer = setTimeout(function(evt) {
            self.runnable.call(self.scope);
            clearTimeout(self.timer);
            self.timer = 0;
        }, 0);
    }

    /**
     * 执行延迟之后异步执行。
     * 
     * @param {number} milliseconds 指定延迟时长，单位：毫秒。
     */
    delay(milliseconds) {
        let self = this;
        let t = setTimeout(function () {
            self.start();
            clearTimeout(t);
        }, milliseconds);
    }
}
