import { Contact } from "./Contact";
import { Device } from "./Device";
import { AuthService } from "../auth/AuthService";

/**
 * 用户描述自己的账号。
 * @extends Contact
 */
export class Self extends Contact {

    /**
     * 构造函数。
     * @param {number|string} id 指定自终端的 ID 。
     * @param {string} [name] 指定自终端的名称。
     */
    constructor(id, name) {
        super(id, name, AuthService.DOMAIN);

        /**
         * 当前设备描述。
         * @private
         * @type {Device}
         */
        this.device = new Device('Web');

        this.addDevice(this.device);
    }

    /**
     * 获取自己的设备信息。
     * @returns {Device} 返回自己的设备实例。
     */
    getDevice() {
        return this.device;
    }

    /**
     * 重置设备。
     * @param {Device} device 
     */
    resetDevice(device) {
        this.removeDevice(this.device);
        this.device = device;
        this.addDevice(this.device);
    }

    /**
     * @inheritdoc
     */
    toJSON() {
        let json = super.toJSON();
        json["device"] = this.device.toJSON();
        return json;
    }

    /**
     * @inheritdoc
     */
    toCompactJSON() {
        let json = super.toCompactJSON();
        json["device"] = this.device.toCompactJSON();
        return json;
    }
}
