import { Utils } from "../utils/Utils";
import { Logger } from "../utils/Logger";
import pako from "pako";

/**
 * 消息描述类。
 */
export class Message {
    /**
     * @param {Array|Uint8Array} payload 消息负载。
     */
    constructor(payload) {
        /**
         * 负载数据。
         * @type {Array|Uint8Array}
         */
        this.payload = payload;

        /**
         * 消息携带的上下文。
         * @type {object}
         */
        this.context = null;

        /**
         * 当前负载是否是压缩格式。
         * @private
         * @type {boolean}
         */
        this.compressible = false;

        // 校验负载是否是压缩格式。
        this.verifyCompression();
    }

    /**
     * 设置消息有效负载数据。
     *
     * @param {Array|Uint8Array} payload 指定有效负载数据。
     */
    setPayload(payload) {
        this.payload = payload;
    }

    /**
     * 设置消息的自定义上下文对象。
     *
     * @param {object} context 指定上下文对象。
     */
    setContext(context) {
        this.context = context;
    }

    /**
     * 获取消息有效负载数据。
     *
     * @returns {Array|Uint8Array} 返回消息有效负载数据。
     */
    getPayload() {
        return this.payload;
    }

    /**
     * 获取消息的自定义上下文对象。
     * 
     * @returns {object} 返回消息的自定义上下文对象。
     */
    getContext() {
        return this.context;
    }

    /**
     * 校验负载是否是压缩格式。
     *
     * @private
     * @returns {boolean} 返回是否压缩。
     */
    verifyCompression() {
        if ((this.payload[0] == 120 && this.payload[1] == -100)
            || this.payload[0] == 120 && this.payload[1] == 156) {
            this.compressible = true;
        }
        else {
            this.compressible = false;
        }
        return this.compressible;
    }

    /**
     * 消息内容是否需要压缩。
     * 
     * @returns {boolean} 如果消息需要压缩返回 {@linkcode true} 。
     */
    needCompressible() {
        return (this.payload.length >= Message.CompressionThreshold);
    }

    /**
     * 打包输入的数据。将数据打包为可被连接发送的格式。
     * 
     * @param {Array|Uint8Array} data 指定待打包的数据。
     * @param {Array} key 指定数据加密的密钥。
     * @returns {Array|Uint8Array} 返回经过打包的数据。
     */
    static pack(data, key) {
        var output = null;

        // 加密
        var buf = null;
        if (null != key) {
            buf = Utils.simpleEncrypt(data, key);
        }
        else {
            buf = data;
        }

        // 压缩
        if (buf.length > Message.CompressionThreshold) {
            output = Message.compress(buf);
        }
        else {
            output = buf;
        }

        return output;
    }

    /**
     * 解包输入的数据。将数据解包为可被处理的格式。
     * 
     * @param {Array|Uint8Array} data 指定待解包的数据。
     * @param {Array} key 指定数据解密的密钥。
     * @returns {Array|Uint8Array} 返回解包之后的数据。
     */
    static unpack(data, key) {
        var output = null;

        // 解压
        var buf = null;
        if (data[0] == 120 && data[1] == 156) {
            buf = Message.uncompress(data);
        }
        else {
            buf = data;
        }

        if (null == buf) {
            // 解压失败
            return null;
        }

        // 解密
        if (null != key) {
            output = Utils.simpleDecrypt(buf, key);
        }
        else {
            output = buf;
        }

        return output;
    }

    /**
     * 压缩数据。
     * 
     * @private
     * @param {Uint8Array} input 
     * @returns {Uint8Array}
     */
    static compress(input) {
        return pako.deflate(input);
    }

    /**
     * 解压数据。
     * 
     * @private
     * @param {Uint8Array} input 
     * @returns {Uint8Array}
     */
    static uncompress(input) {
        var result = null;
        try {
            result = pako.inflate(input);
        } catch (err) {
            Logger.e("Message", "Uncompress error: " + err);
        }
        return result;
    }
}

/**
 * 系统默认的消息头格式。
 * @type {array}
 */
Message.DefaultHead = [ 0x20, 0x13, 0x09, 0x08 ];

/**
 * 系统默认的消息尾格式。
 * @type {array}
 */
Message.DefaultTail = [ 0x19, 0x78, 0x10, 0x04 ];

/**
 * 压缩阀值，单位：字节。当消息负载长度超过该值时，将对打包的数据进行压缩。
 * 建议最小值为 120 字节。
 * @type {number}
 * @default 1024
 */
Message.CompressionThreshold = 1024;
