/**
 * 版本信息描述类。
 */
export class Version {
    /**
     */
    constructor() {
        /**
         * 主版本号。
         * @type {number}
         */
        this.MAJOR = 2;

        /**
         * 副版本号。
         * @type {number}
         * */
        this.MINOR = 3;

        /**
         * 修订号。
         * @type {number}
         */
        this.REVISION = 9;

        /**
         * 版本名。
         * @type {string}
         */
        this.NAME = "Genie";
    }

    /**
     * 获得字符串形式的版本描述。
     *
     * @returns {string} 返回字符串形式的版本描述。
     */
    getNumbers() {
        let buf = [ this.MAJOR, '.', this.MINOR, '.', this.REVISION ];
        return buf.join('');
    }

    /**
     * 返回字符串形式的版本信息，包含版本名。
     * 
     * @returns {string} 返回字符串形式的版本信息，包含版本名。
     */
    toString() {
        let buf = [ this.MAJOR, '.', this.MINOR, '.', this.REVISION, ' (', this.NAME, ')' ];
        return buf.join('');
    }
}
