import { Primitive } from "../Primitive";
import { Stuff } from "../Stuff";
import { LiteralBase } from "../LiteralBase";

/**
 * 动作方言，可用于封装一个 Action 行为的数据。
 */
export class ActionDialect extends Primitive {

    /**
     * @param {string|Primitive} mix 指定动作名或源数据。
     */
    constructor(mix) {
        super();
        if (typeof mix === 'string') {
            this.stuffList.push(new Stuff(mix.toString(), LiteralBase.STRING));
        }
        else {
            this.sn = mix.sn;
            this.stuffList = mix.stuffList;
        }
    }

    /**
     * 设置动作名。
     *
     * @param {string} name 指定动作名。
     */
    setName(name) {
        this.stuffList[0] = new Stuff(name, LiteralBase.STRING);
    }

    /**
     * 获取动作名。
     *
     * @returns {string} 返回动作名。
     */
    getName() {
        return this.stuffList[0].getValueAsString();
    }

    /**
     * 获取参数数量。
     *
     * @returns {number} 返回参数数量。
     */
    numParams() {
        return parseInt((this.stuffList.length - 1) / 2);
    }

    /**
     * 是否包含指定键的参数。
     *
     * @param key 指定待判断的键。
     * @returns {boolean} 如果存在返回 {@linkcode true} 。
     */
    containsParam(key) {
        for (var i = 1, size = this.stuffList.length; i < size; i += 2) {
            var sk = this.stuffList[i].getValueAsString();
            if (sk == key) {
                return true;
            }
        }
        return false;
    }

    /**
     * 添加指定字面义的键值对参数。
     *
     * @param {string} key 指定数据的键。
     * @param {*} value 指定数据的值。
     * @param {number} [literal] 指定数据值的字面义。
     * @see LiteralBase
     */
    addParam(key, value, literal) {
        var index = this.keyIndex(key);
        if (index > 0) {
            this.stuffList[index + 1] = new Stuff(value, literal);
        }
        else {
            this.stuffList.push(new Stuff(key, LiteralBase.STRING));
            this.stuffList.push(new Stuff(value, literal));
        }
    }

    /**
     * 获取指定键的参数值。
     *
     * @param {string} key 指定需要获取数据的键。
     * @returns {*} 返回指定键的参数值。如果没有找到该键的数据返回 {@linkcode null} 。
     */
    getParam(key) {
        var value = this.getValueStuff(key);
        return (null == value) ? null : value.getLiteralValue();
    }

    /**
     * 获取指定键的字符串形式的参数值。
     *
     * @param {string} key 指定需要获取数据的键。
     * @returns {string} 返回指定键的字符串形式的参数值。如果没有找到该键的数据返回 {@linkcode null} 。
     */
    getParamAsString(key) {
        var value = this.getValueStuff(key);
        return (null == value) ? null : value.getValueAsString();
    }

    /**
     * 获取指定键的整数形式的参数值。
     *
     * @param {string} key 指定需要获取数据的键。
     * @returns {number} 返回指定键的整数形式的参数值。如果没有找到该键的数据返回 {@linkcode null} 。
     */
    getParamAsInt(key) {
        var value = this.getValueStuff(key);
        return (null == value) ? null : value.getValueAsInt();
    }

    /**
     * 获取指定键的长整型形式的参数值。
     *
     * @param {string} key 指定需要获取数据的键。
     * @returns {number} 返回指定键的长整型形式的参数值。如果没有找到该键的数据返回 {@linkcode null} 。
     */
    getParamAsLong(key) {
        var value = this.getValueStuff(key);
        return (null == value) ? null : value.getValueAsLong();
    }

    /**
     * 获取指定键的浮点型形式的参数值。
     * 
     * @param {string} key 指定需要获取数据的键。
     * @returns {number} 返回指定键的浮点型形式的参数值。如果没有找到该键的数据返回 {@linkcode null} 。
     */
    getParamAsFloat(key) {
        var value = this.getValueStuff(key);
        return (null == value) ? null : value.getValueAsFloat();
    }

    /**
     * 获取指定键的双精浮点型形式的参数值。
     * 
     * @param {string} key 指定需要获取数据的键。
     * @returns {number} 返回指定键的双精浮点型形式的参数值。如果没有找到该键的数据返回 {@linkcode null} 。
     */
    getParamAsDouble(key) {
        var value = this.getValueStuff(key);
        return (null == value) ? null : value.getValueAsDouble();
    }

    /**
     * 获取指定键的布尔型形式的参数值。
     *
     * @param {string} key 指定需要获取数据的键。
     * @returns {boolean} 返回指定键的布尔型形式的参数值。如果没有找到该键的数据返回 {@linkcode null} 。
     */
    getParamAsBool(key) {
        var value = this.getValueStuff(key);
        return (null == value) ? null : value.getValueAsBool();
    }

    /**
     * 获取指定键的 JSON 形式的参数值。
     *
     * @param {string} key 指定需要获取数据的键。
     * @returns {object} 返回指定键的 JSON 形式的参数值。如果没有找到该键的数据返回 {@linkcode null} 。
     */
    getParamAsJson(key) {
        var value = this.getValueStuff(key);
        return (null == value) ? null : value.getValueAsJson();
    }

    /**
     * 获取指定键对应的 Stuff 的值。
     * 
     * @param {string} key 指定需要获取数据的键。
     * @returns {Stuff} 返回指定键对应的 Stuff 的值。如果没有找到该键的数据返回 {@linkcode null} 。
     */
    getValueStuff(key) {
        for (var i = 1, size = this.stuffList.length; i < size; i += 2) {
            var sk = this.stuffList[i].getValueAsString();
            if (sk == key) {
                return this.stuffList[i + 1];
            }
        }
        return null;
    }

    /**
     * 键所在的 Stuff List 索引。
     *
     * @private
     * @param {string} key 指定键。
     * @returns {number} 返回键的索引位置。
     */
    keyIndex(key) {
        for (var i = 1, size = this.stuffList.length; i < size; i += 2) {
            var sk = this.stuffList[i].getValueAsString();
            if (sk == key) {
                return i;
            }
        }
        return -1;
    }
}
