import { UUID } from "../utils/UUID";

/**
 * 内核标签。
 */
export class NucleusTag {
    /**
     * @param {string} value UUID 格式字符串。
     */
    constructor(value) {
        /**
         * UUID 格式的唯一标识。
         * @type {string}
         */
        this.uuid = (value !== undefined) ? value : UUID.v4().toString();
    }

    /**
     * 重置内核标签数据。
     */
    reset() {
        this.uuid = UUID.v4().toString();
    }

    /**
     * 获得标签的字符串形式。
     *
     * @returns {string} 返回字符串形式的标签。
     */
    asString() {
        return this.uuid;
    }

    /**
     * 获得标签的字符串形式。
     * 
     * @returns {string} 返回字符串形式的标签。
     */
    toString() {
        return this.uuid;
    }
}
