/**
 * 会话器状态描述。
 * 
 * @interface
 * @prop {number} Disconnected 连接已断开。
 * @prop {number} Connecting 正在连接。
 * @prop {number} Connected 连接已建立。
 * @prop {number} Disconnecting 正在断开连接。
 */
export const SpeakState = {
    /**
     * 连接已断开。
     */
    Disconnected: 1,

    /**
     * 正在连接。
     */
    Connecting: 2,

    /**
     * 连接已建立。
     */
    Connected: 3,

    /**
     * 正在断开连接。
     */
    Disconnecting: 4
}
