/**
 * 脸部监视器事件。
 */
export const FaceMonitorEvent = {
    
    /**
     * 模块就绪，可以开始进行工作。
     * @type {string}
     */
    Ready: 'Ready',

    /**
     * 加载模型。
     * @type {string}
     */
    Load: 'Load',

    /**
     * 已加载模型。
     * @type {string}
     */
    Loaded: 'Loaded',

    /**
     * 手部触碰/遮挡脸部。
     * @type {string}
     */
    Touched: 'Touched',

    /**
     * 出现错误。
     * @type {string}
     */
    Error: 'Error'
}
