/**
 * 接触记录。
 */
export class TouchedRecord {

    /**
     * @param {boolean} touched 
     * @param {number} numPixels 
     * @param {number} facePixels 
     * @param {number} score 
     * @param {number} touchScore 
     */
    constructor(touched, numPixels, facePixels, score, touchScore) {
        /**
         * @type {boolean}
         */
        this.touched = touched;

        /**
         * @type {number}
         */
        this.numPixels = numPixels;

        /**
         * @type {number}
         */
        this.facePixels = facePixels;

        /**
         * @type {number}
         */
        this.score = score;

        /**
         * @type {number}
         */
        this.touchScore = touchScore;

        /**
         * @type {number}
         */
        this.touch = score / facePixels;
    }

    toString() {
        return `Face Pixels %: ${this.facePixels / this.numPixels}, Score: ${this.score}, TouchScore: ${this.touchScore}, ` +
                `Touch %: ${this.touch}`;
    }
}
