/**
 * 消息错误码。
 * 
 * @interface
 * @prop {number} UNKNOWN 未知的错误。
 * @prop {number} ADDRESS_INVALID 无效的网络地址。
 * @prop {number} STATE_ERROR 消息处理器状态错误。
 * @prop {number} SOCKET_FAILED Socket 函数发生错误。
 * @prop {number} BIND_FAILED 绑定服务时发生错误。
 * @prop {number} LISTEN_FAILED 监听连接时发生错误。
 * @prop {number} ACCEPT_FAILED Accept 发生错误。
 * @prop {number} CONNECT_FAILED 连接失败。
 * @prop {number} CONNECT_TIMEOUT 连接超时。
 * @prop {number} WRITE_TIMEOUT 写数据超时。
 * @prop {number} READ_TIMEOUT 读数据超时。
 * @prop {number} WRITE_FAILED 写入数据时发生错误。
 * @prop {number} READ_FAILED 读取数据时发生错误。
 * @prop {number} WRITE_OUTOFBOUNDS 写数据越界。
 */
export const MessageErrorCode = {

    /** 未知的错误。 */
    UNKNOWN: 100,

    /** 无效的网络地址。 */
    ADDRESS_INVALID: 101,
    /** 消息处理器状态错误。 */
    STATE_ERROR: 102,

    /** Socket 函数发生错误。 */
    SOCKET_FAILED: 200,
    /** 绑定服务时发生错误。 */
    BIND_FAILED: 201,
    /** 监听连接时发生错误。 */
    LISTEN_FAILED: 202,
    /** Accept 发生错误。 */
    ACCEPT_FAILED: 203,

    /** 连接失败。 */
    CONNECT_FAILED: 300,
    /** 连接超时。 */
    CONNECT_TIMEOUT: 301,

    /** 写数据超时。 */
    WRITE_TIMEOUT: 401,
    /** 读数据超时。 */
    READ_TIMEOUT: 402,
    /** 写入数据时发生错误。 */
    WRITE_FAILED: 403,
    /** 读取数据时发生错误。 */
    READ_FAILED: 404,
    /** 写数据越界。 */
    WRITE_OUTOFBOUNDS: 405
}
