import { Thread } from "../utils/Thread";
import { Logger } from "../utils/Logger";
import { Utils } from "../utils/Utils";
import { InetAddress } from "../utils/InetAddress";
import { Aggregation } from "../utils/Aggregation";
import { NucleusTag } from "../api/NucleusTag";
import { MessageConnector } from "../net/MessageConnector";
import { MessageConnectorSession } from "../net/MessageConnectorSession";
import { HeartbeatMachine } from "./HeartbeatMachine";
import { HeartbeatMachineListener } from "./HeartbeatMachineListener";
import { PrimitiveCapsule } from "./PrimitiveCapsule";
import { Protocol } from "./Protocol";
import { TalkError } from "./TalkError";
import { Message } from "../net/Message";
import { MessageErrorCode } from "../net/MessageErrorCode";
import { MessageHandler } from "../net/MessageHandler";
import { MessageContext } from "./MessageContext";
import { SpeakState } from "./SpeakState";
import { Primitive } from "./Primitive";

/**
 * Talk 会话客户端实现。
 */
export class Speaker extends Aggregation(MessageHandler, HeartbeatMachineListener) {

    /**
     * @param {NucleusTag} tag 指定内核标签。
     * @param {InetAddress} address 指定连接地址。
     * @param {TalkListener} listener 指定会话监听器。
     */
    constructor(tag, address, listener) {
        super();

        /**
         * 内核标签。
         * @type {NucleusTag}
         */
        this.tag = tag;

        /**
         * 连接地址。
         * @type {InetAddress}
         */
        this.address = address; 

        /**
         * 监听器。
         * @type {TalkListener}
         */
        this.listener = listener;

        /**
         * 已连接的服务器的标签。
         * @type {string}
         */
        this.serverTag = null;

        /**
         * 连接状态描述。
         * @type {SpeakState}
         */
        this.state = SpeakState.Disconnected;

        /**
         * 连接器。
         * @type {MessageConnector}
         */
        this.connector = null;

        /**
         * 会话上下文。
         * @type {MessageConnectorSession}
         */
        this.session = null;

        /**
         * 通信密钥。
         * @type {Array}
         */
        this.secretKey = null;

        /**
         * 已发送的原语信息列表。
         * @type {PrimitiveCapsule}
         */
        this.primitiveCapsule = null;

        /**
         * 重连定时器。
         * @type {number}
         * @private
         */
        this.reconnectTimer = 0;

        /**
         * 是否允许重连。
         * @type {boolean}
         */
        this.reconnectEnabled = true;

        /**
         * 握手定时器。
         * @type {number}
         * @private
         */
        this.handshakeTimer = 0;

        /**
         * 握手超时。
         * @type {number}
         * @private
         */
        this.handshakeTimeout = 10000;

        /**
         * 心跳协议管理控制器。
         * @type {HeartbeatMachine}
         */
        this.hm = null;

        // 应答跟踪器
        //this.acknowledgement = null;
    }

    /**
     * 启动并开始连接服务器。
     * 
     * @returns {boolean} 返回是否启动成功。
     */
    start() {
        if (null == this.address) {
            return false;
        }

        if (this.state == SpeakState.Connecting || this.state == SpeakState.Connected) {
            return true;
        }

        this.state = SpeakState.Connecting;

        if (this.reconnectTimer > 0) {
            clearTimeout(this.reconnectTimer);
            this.reconnectTimer = 0;
        }

        // 启动应答跟踪
        // this.acknowledgement.start();

        // 初始化连接器
        this.connector = new MessageConnector();
        this.connector.setHandler(this);

        // 初始化 HM
        this.hm = new HeartbeatMachine(this.connector);
        this.hm.setListener(this);

        // 进行连接
        if (!this.connector.connect(this.address)) {
            this.state = SpeakState.Disconnected;
            this.connector.setHandler(null);
            this.connector = null;

            this.hm.shutdown();
            this.hm.setListener(null);
            this.hm = null;

            var that = this;
            this.reconnectTimer = setTimeout(function() {
                Logger.i('Speaker', 'Restart speaker: ' + that.address.toString());
                that.stop();
                that.reconnectEnabled = true;
                that.start();
            }, 5000);
        }

        // 重置为允许重连
        this.reconnectEnabled = true;

        return true;
    }

    /**
     * 停止与服务器的连接。
     */
    stop() {
        if (SpeakState.Disconnecting == this.state || SpeakState.Disconnected == this.state) {
            return;
        }

        Logger.i('Speaker', 'Stop speaker ' + this.address.toString());

        this.reconnectEnabled = false;

        this.state = SpeakState.Disconnecting;

        if (this.reconnectTimer > 0) {
            clearTimeout(this.reconnectTimer);
            this.reconnectTimer = 0;
        }
 
        if (null != this.connector) {
            this.connector.disconnect();
        }

        if (null != this.hm) {
            this.hm.shutdown();
            this.hm.setListener(null);
            this.hm = null;
        }
        if (this.handshakeTimer > 0) {
            clearTimeout(this.handshakeTimer);
            this.handshakeTimer = 0;
        }

        this.state = SpeakState.Disconnected;
    }

    /**
     * 向指定 Cellet 发送原语数据。
     * 
     * @param {string} cellet 指定 Cellet 名称。
     * @param {Primitive} primitive 指定待发送原语。
     * @param {boolean} [ack] 可选参数，指定是否需要服务器应答。
     * @returns {boolean} 连接状态正常返回 {@linkcode true} 。
     */
    speak(cellet, primitive, ack) {
        if (this.state != SpeakState.Connected) {
            return false;
        }

        // 默认 NoAck
        if (ack === undefined) {
            ack = false;
        }

        if (ack) {
            // 记录
            this.primitiveCapsule.addPrimitive(cellet, primitive);
        }

        // 序列化数据
        var buf = Protocol.serializeSpeak(cellet, primitive, ack);

        var message = new Message(buf);
        message.setContext(new MessageContext(cellet, primitive));

        // 发送数据
        this.connector.write(message);

        return true;
    }

    /**
     * @inheritdoc
     */
    sessionCreated(session) {
        Logger.d("Speaker", "sessionCreated");

        this.session = session;

        // 创建对应发送管理容器
        this.primitiveCapsule = new PrimitiveCapsule(session);
    }

    /**
     * @inheritdoc
     */
    sessionDestroyed(session) {
        Logger.d("Speaker", "sessionDestroyed");

        // 如果 connector 不为空，则尝试恢复连接
        if (null != this.connector) {
            // 先关闭
            this.stop();
        }

        // 移除所有数据
        if (null != this.primitiveCapsule) {
            this.primitiveCapsule.clean();
            this.primitiveCapsule = null;
        }

        // 关闭 HM
        if (null != this.hm) {
            this.hm.removeSession(session);
            this.hm.shutdown();
            this.hm = null;
        }

        if (this.reconnectEnabled) {
            var that = this;
            this.reconnectTimer = setTimeout(function() {
                Logger.i('Speaker', 'Reconnect ' + that.address.toString());
                that.stop();
                that.reconnectEnabled = true;
                that.start();
            }, 5000);
        }
    }

    /**
     * @inheritdoc
     */
    sessionOpened(session) {
        Logger.d("Speaker", "sessionOpened");

        this.state = SpeakState.Connecting;

        // 添加会话
        this.hm.addSession(session, this.tag.toString());

        // 清空密钥
        this.secretKey = null;

        var that = this;
        var thread = new Thread(function () {
            that.handshake();
        });
        thread.start();
    }

    /**
     * @inheritdoc
     */
    sessionClosed(session) {
        Logger.d("Speaker", "sessionClosed");

        this.state = SpeakState.Disconnected;

        if (null != this.hm) {
            this.hm.shutdown();
            this.hm.removeSession(session);
            this.hm.setListener(null);
            this.hm = null;
        }
        if (this.handshakeTimer > 0) {
            clearTimeout(this.handshakeTimer);
            this.handshakeTimer = 0;
        }

        this.listener.onQuitted(this);
    }

    /**
     * @inheritdoc
     */
    messageReceived(session, message) {
        var that = this;
        if (session && message) {
            var protocol = Protocol.recognize(message.payload);

            if (Protocol.SpeakNoAck == protocol) {
                that.processSpeak(session, message, false);
            }
            else if (Protocol.SpeakAck == protocol) {
                that.processSpeak(session, message, true);
            }
            else if (Protocol.Ack == protocol) {
                that.processAck(session, message);
            }
            else if (Protocol.Heartbeat == protocol) {
                that.processHeartHeat(session, message);
            }
            else if (Protocol.HeartbeatAck == protocol) {
                that.processHeartHeatAck(session, message);
            }
            else if (Protocol.Handshake == protocol) {
                var thread = new Thread(function() {
                    that.processHandshake(session, message);
                });
                thread.start();
            }
            else {
                Logger.w("Speaker", "Unknown protocol");
            }
        }
    }

    /**
     * @inheritdoc
     */
    messageSent(session, message) {
        var protocol = Protocol.recognize(message.getPayload());
        if (Protocol.SpeakNoAck == protocol || Protocol.SpeakAck == protocol) {
            var ctx = message.getContext();
            this.listener.onSpoke(this, ctx.target, ctx.primitive)
        }
        else if (Protocol.Handshake == protocol) {
            // 设置密钥
            this.session.setSecretKey(this.secretKey);
        }
    }

    /**
     * @inheritdoc
     */
    errorOccurred(errorCode, session) {
        if (this.reconnectTimer > 0) {
            clearTimeout(this.reconnectTimer);
        }

        var that = this;
        this.reconnectTimer = setTimeout(function() {
            Logger.i('Speaker', 'Reconnect ' + that.address.toString());
            that.stop();
            that.reconnectEnabled = true;
            that.start();
        }, (errorCode == MessageErrorCode.SOCKET_FAILED) ? 5000 : 3000);

        var fault = TalkError.transformError(errorCode);
        this.listener.onFailed(this, fault);
    }

    /**
     * @inheritdoc
     */
    onSessionTimeout(session, duration) {
        var fault = new TalkError(TalkError.HeartbeatTimeout);
        this.listener.onFailed(this, fault);
    }

    /**
     * 发送握手协议进行握手。
     * 
     * @private
     */
    handshake() {
        if (Logger.isDebugLevel()) {
            Logger.d("Speaker", "Handshake : " + this.address.toString());
        }

        // 启动定时器
        if (this.handshakeTimer > 0) {
            clearTimeout(this.handshakeTimer);
        }
        var that = this;
        this.handshakeTimer = setTimeout(function() {
            var fault = new TalkError(TalkError.HandshakeTimeout);
            that.listener.onFailed(that, fault);

            clearTimeout(that.handshakeTimer);
            that.handshakeTimer = 0;

            that.stop();
        }, this.handshakeTimeout);

        // 秘钥
        this.secretKey = Utils.toCharCode(Utils.randomString(8));

        // 序列化
        var buf = Protocol.serializeHandshake(this.secretKey, this.tag.asString());

        var message = new Message(buf);
        try {
            this.connector.write(message);
        } catch (e) {
            Logger.e("Speaker", "Handshake failed: " + e);
        }
    }

    /**
     * 处理接收到的握手协议。
     * 
     * @private
     * @param {MessageConnectorSession} session 会话上下文。
     * @param {Message} message 消息实体。
     */
    processHandshake(session, message) {
        var prtl = Protocol.deserializeHandshake(message.payload);
        if (prtl.key != null && prtl.tag != null) {
            if (Logger.isDebugLevel()) {
                Logger.d("Speaker", "Handshake Success");
            }

            if (this.handshakeTimer > 0) {
                clearTimeout(this.handshakeTimer);
                this.handshakeTimer = 0;
            }

            if (this.state == SpeakState.Disconnected) {
                // 已经有失败的请求，则不再记录成功的请求
                return;
            }

            // 启动心跳管理
            this.hm.startup();

            // 记录 tag
            this.serverTag = prtl.tag.toString();

            // 更新状态
            this.state = SpeakState.Connected;

            this.listener.onContacted(this);
        }
        else {
             // 关闭连接
             this.stop();

             // 回调失败
             var fault = new TalkError(TalkError.HandshakeError);
             this.listener.onFailed(this, fault);
        }
    }

    /**
     * 处理接收到的心跳协议。
     * 
     * @private
     * @param {MessageConnectorSession} session 会话上下文。
     * @param {Message} message 消息实体。
     */
    processHeartHeat(session, message) {
        this.hm.processHeartbeat(session, message.payload, Date.now());
    }

    /**
     * 处理接收到的心跳应答协议。
     * 
     * @private
     * @param {MessageConnectorSession} session 会话上下文。
     * @param {Message} message 消息实体。
     */
    processHeartHeatAck(session, message) {
        this.hm.processHeartbeatAck(session, message.payload);
    }

    /**
     * 处理接收到的会话协议。
     * 
     * @private
     * @param {MessageConnectorSession} session 会话上下文。
     * @param {Message} message 消息实体。
     * @param {boolean} ack 是否需要应答。
     */
    processSpeak(session, message, ack) {
        if (this.state != SpeakState.Connected) {
            // 已经有失败的请求，则不再记录成功的请求
            return;
        }

        var protocol = Protocol.deserializeSpeak(message.payload);

        if (protocol.target != null && protocol.primitive != null) {
            if (ack) {
                this.ack(protocol.target, protocol.primitive);
            }

            this.listener.onListened(this, protocol.target, protocol.primitive);
        }
    }

    /**
     * 处理接收到的应答协议。
     * 
     * @private
     * @param {MessageConnectorSession} session 会话上下文。
     * @param {Message} message 消息实体。
     */
    processAck(session, message) {
        var protocol = Protocol.deserializeAck(message.payload);
        var primitive = this.primitiveCapsule.removePrimitive(protocol.target, protocol.sn);
        if (null != protocol) {
            this.listener.onAck(this, protocol.target, primitive);
        }
    }

    /**
     * 发送 Ack 应答。
     *
     * @private
     * @param {string} cellet 目标 Cellet 名称。
     * @param {Primitive} primitive 应答的原语。
     */
    ack(cellet, primitive) {
        if (this.state != SpeakState.Connected) {
            return false;
        }

        if (null != this.connector) {
            // 序列化数据
            var buf = Protocol.serializeAck(cellet, primitive);

            var message = new Message(buf);

            // 将数据写入队列
            this.connector.write(message);
        }

        return true;
    }
}
