/**
 * 变量字面意义。
 * 
 * @interface
 * @prop {number} STRING 字符串型。
 * @prop {number} INT 整数型。
 * @prop {number} LONG 长整数型。
 * @prop {number} FLOAT 浮点类型。
 * @prop {number} DOUBLE 双精浮点型。
 * @prop {number} BOOL 布尔型。
 * @prop {number} JSON JSON 类型。
 * @prop {number} BIN 二进制类型。
 */
export const LiteralBase = {
    /** 字符串型。*/
    STRING: 83,     // 'S',
    /** 整数型。*/
    INT: 73,        // 'I',
    /** 长整数型。*/
    LONG: 76,       // 'L',

    /**
     * 浮点类型。JavaScript 版本使用字符形式浮点数。
     */
    FLOAT: 102,      // 'f',
    /**
     * 双精浮点型。JavaScript 版本使用字符形式浮点数。
     */
    DOUBLE: 100,    // 'd',

    /** 布尔型。*/
    BOOL: 66,       // 'B',
    /** JSON 类型。*/
    JSON: 74,       // 'J',
    /**  二进制类型。*/
    BIN: 78        // 'N',
}
