import { Thread } from "../utils/Thread";
import { Logger } from "../utils/Logger";
import { LogLevel } from "../utils/LogLevel";
import { MessageConnectorSession } from "./MessageConnectorSession";
import { Message } from "./Message";
import { MessageService } from "./MessageService";
import { MessageErrorCode } from "./MessageErrorCode";

/**
 * 非阻塞连接器。数据发送时不会阻塞调用函数。
 * 该连接器使用 WebSocket 进行通信。
 */
export class MessageConnector extends MessageService {

    constructor() {
        super();
        /**
         * 单次写数据大小限制，单位：字节。
         * @type {number}
         * @default 32768
         */
        this.writeLimit = 32768;

        /**
         * 连接器连接的地址。
         * @type {InetAddress}
         */
        this.address = null;

        /**
         * 连接超时时间，单位：毫秒。
         * @type {number}
         * @default 10000
         */
        this.connectTimeout = 10000;

        /**
         * 连接定时器。
         * @type {number}
         * @private
         */
        this.connectTimer = 0;

        /**
         * 连接器使用的 WebSocket 实例。
         * @type {WebSocket}
         */
        this.socket = null;

        /**
         * 对应的会话对象。
         * @type {MessageConnectorSession}
         */
        this.session = null;

        /**
         * 连接是否被关闭。
         * @type {boolean}
         */
        this.closed = true;

        /**
         * 发送消息列表。保存准备发送的消息。
         * @type {Array}
         */
        this.messages = null;

        /**
         * 是否采用安全连接。
         * @type {boolean}
         * @private
         */
        this.secure = (window.location.protocol.toLowerCase().indexOf("https") >= 0);

        /**
         * 接收消息的 Buffer 。
         * @type {Uint8Array}
         */
        this.receivedBuf = [];
    }

    /**
     * 返回连接地址。
     * 
     * @returns {InetAddress} 返回连接地址。
     */
    getAddress() {
        return this.address;
    }

    /**
     * 连接到指定地址的服务器。
     * 
     * @param {InetAddress} address 指定连接地址。
     * @returns {boolean} 返回是否成功创建连接。
     */
    connect(address) {
        if (this.socket != null && this.socket.readyState == WebSocket.OPEN) {
            Logger.w("MessageConnector", "Connector has connected to " + address.toString());
            return false;
        }

        if (!this.closed && null != this.socket) {
            try {
                this.socket.close(1000, "close");
            } catch (err) {
                // Nothing
            }
            this.destroySocketEvent();
            this.socket = null;
            this.closed = true;
        }

        if (this.connectTimer > 0) {
            clearTimeout(this.connectTimer);
            this.connectTimer = 0;
        }

        // 重置队列
        this.messages = [];
        // 记录连接地址
        this.address = address;

        let self = this;

        // 创建 Session
        this.session = new MessageConnectorSession(this.address);

        self.fireSessionCreated();

        this.connectTimer = setTimeout(function() {
            // 超时处理
            self.fireErrorOccurred(MessageErrorCode.CONNECT_TIMEOUT);
        }, this.connectTimeout);

        // 创建 WebSocket
        this.socket = this.createSocket(this.address.getHost(), this.address.getPort());
        if (null == this.socket) {
            let notifyThread = new Thread(function() {
                // 通知 Session 销毁。
                self.fireSessionDestroyed();

                if (self.connectTimer > 0) {
                    clearTimeout(self.connectTimer);
                    self.connectTimer = 0;
                }
            });
            notifyThread.start();
            return false;
        }

        return true;
    }

    /**
     * 断开与服务器的连接。
     */
    disconnect() {
        if (this.connectTimer > 0) {
            clearTimeout(this.connectTimer);
            this.connectTimer = 0;
        }

        if (null != this.socket) {
            try {
                this.socket.close(1000, "disconnect");
            } catch (e) {
                Logger.e("MessageConnector", "Close socket has exception.");
            }
        }
        else {
            this.fireSessionDestroyed();
        }

        this.destroySocketEvent();
        this.socket = null;
    }

    /**
     * 用于销毁当前 socket 事件 (避免旧socket影响新的socket)
     * @private
     */
    destroySocketEvent() {
        if (null == this.socket) {
            return;
        }

        this.socket.onopen = (e) => {};
        this.socket.onclose = (e) => {};
        this.socket.onmessage = (e) => {};
        this.socket.onerror = (e) => {};
    }

    /**
     * 创建 WebSocket 。
     * 
     * @private
     * @param {string} host 连接地址。
     * @param {number} port 连接端口。
     * @returns {WebSocket} 返回 WebSocket 。
     */
    createSocket(host, port) {
        if (undefined === window.WebSocket) {
            return null;
        }

        let socket = null;
        try {
            if (this.secure) {
                if (port !== undefined) {
                    socket = new WebSocket("wss://" + host + ":" + port + "/cell/", "cell");
                } else {
                    socket = new WebSocket("wss://" + host + "/cell/", "cell");
                }
            }
            else {
                if (port !== undefined) {
                    socket = new WebSocket("ws://" + host + ":" + port + "/cell/", "cell");
                } else {
                    socket = new WebSocket("ws://" + host + "/cell/", "cell");
                }
            }
        } catch (e) {
            this.fireErrorOccurred(MessageErrorCode.SOCKET_FAILED);
            console.log('WebSocket: ' + e);
        }

        if (null == socket) {
            return null;
        }

        // 使用 'arraybuffer' 数据
        socket.binaryType = 'arraybuffer';

        socket.onopen = (event) => {
            this.fireSessionOpened(event);
        };
        socket.onclose = (event) => {
            this.fireSessionClosed(event);
        };
        socket.onmessage = (event) => {
            this.receive(event);
        };
        socket.onerror = (event) => {
            this.fireErrorOccurred(MessageErrorCode.SOCKET_FAILED, event);
        };
        return socket;
    }

    /**
     * 设置连接超时。
     * 
     * @param {number} timeout 指定超时时长，单位：毫秒。
     */
    setConnectTimeout(timeout) {
        this.connectTimeout = timeout;
    }

    /**
     * 返回连接超时时间。
     * 
     * @returns {number} 返回超时时间。
     */
    getConnectTimeout() {
        return this.connectTimeout;
    }

    /**
     * 返回会话上下文。
     * 
     * @returns {MessagesConnectorSession} 返回会话上下文。
     */
    getSession() {
        return this.session;
    }

    /**
     * 向连接的远程终端发送数据。
     * 
     * @param {Message} message 指定待发送的消息。
     */
    write(message) {
        if (message == null || message === undefined) {
            this.fireErrorOccurred(MessageErrorCode.WRITE_FAILED);
            return;
        }

        // 写入数组
        this.messages.push(message);

        // 发送
        this._send();
    }

    /**
     * 将数组里的数据写入 Socket 。
     * 
     * @private
     */
    _send() {
        if (this.closed) {
            return;
        }

        try {
            if (this.messages.length > 0) {
                // 取出第一个数据进行发送
                let message = this.messages.shift();
                if (null == message) {
                    return;
                }

                // 打包消息
                let plaintext = message.payload;
                let skey = this.session.secretKey;

                let data = Message.pack(plaintext, skey);

                let trans = new Array();
                for (let i = 0; i < data.length; ++i) {
                    trans.push(data[i]);
                }
                // 添加包尾
                Array.prototype.push.apply(trans, Message.DefaultTail);

                // 创建数据包
                let packet = new Uint8Array(trans);

                // 发送
                this.socket.send(packet);

                if (null != this.handler) {
                    this.handler.messageSent(this.session, message);
                }

                if (this.messages.length > 0) {
                    let self = this;
                    let thr = new Thread(function() {
                        self._send();
                    });
                    thr.delay(10);
                }
            }
        }
        catch (e) {
            Logger.log("MessageConnector", e, LogLevel.WARNING);
        }
    }

    /**
     * 接收处理数据包。
     * 
     * @param {MessageEvent} event 事件。
     */
    receive(event) {
        if (typeof event.data === 'string') {
            Logger.w("MessageConnector", "Not supported string type");
        }
        else {
            let self = this;
            self.receivedBuf = self.receivedBuf.concat(Array.from(new Uint8Array(event.data)));
            if (self.receivedBuf.length > 4) {
                // 判断消息是否接收完
                let last = self.receivedBuf.length - 1;
                if (self.receivedBuf[last - 3] == Message.DefaultTail[0]
                    && self.receivedBuf[last - 2] == Message.DefaultTail[1]
                    && self.receivedBuf[last - 1] == Message.DefaultTail[2]
                    && self.receivedBuf[last] == Message.DefaultTail[3]) {
                    // 解包
                    let packet = self.receivedBuf.slice(0, self.receivedBuf.length - 4);
                    self.process(Message.unpack(packet, self.session.secretKey));
                    self.receivedBuf.length = 0;
                    self.receivedBuf = [];
                }
            }
        }
    }

    /**
     * 处理数据，并回调接口。
     * 
     * @private
     * @param {Uint8Array} data 从 Socket 解包之后的原始数据。
     */
    process(data) {
        if (null != this.handler) {
            this.handler.messageReceived(this.session, new Message(data));
        }
    }

    /**
     * 触发会话创建事件。
     * 
     * @private
     */
    fireSessionCreated() {
        if (null != this.handler) {
            this.handler.sessionCreated(this.session);
        }
    }

    /**
     * 触发会话打开事件。
     * 
     * @private
     * @param {*} event 
     */
    fireSessionOpened(event) {
        this.closed = false;

        if (this.connectTimer > 0) {
            clearTimeout(this.connectTimer);
            this.connectTimer = 0;
        }

        if (null != this.handler) {
            this.handler.sessionOpened(this.session);
        }
    }

    /**
     * 触发会话关闭事件。
     * 
     * @private
     * @param {*} event 
     */
    fireSessionClosed(event) {
        if (this.connectTimer > 0) {
            clearTimeout(this.connectTimer);
            this.connectTimer = 0;
        }

        if (!this.closed) {
            this.closed = true;

            if (null != this.handler) {
                this.handler.sessionClosed(this.session);
            }

            this.fireSessionDestroyed(this.session);
        }

        this.destroySocketEvent();
    }

    /**
     * 触发会话销毁事件。
     * 
     * @private
     */
    fireSessionDestroyed() {
        if (null != this.session && null != this.handler) {
            this.handler.sessionDestroyed(this.session);
        }

        this.destroySocketEvent();

        this.session = null;
        this.socket = null;
    }

    /**
     * 触发发生错误事件。
     * 
     * @private
     * @param {MessageErrorCode} errorCode 消息错误码。
     * @param {*} event
     */
    fireErrorOccurred(errorCode, event) {
        if (this.connectTimer > 0) {
            clearTimeout(this.connectTimer);
            this.connectTimer = 0;
        }

        if (null != this.handler) {
            this.handler.errorOccurred(errorCode, this.session);
        }

        this.destroySocketEvent();
        this.socket = null;
    }
}
