import { FastMap } from "../utils/FastMap";
import { Logger } from "../utils/Logger";
import { MessageConnector } from "../net/MessageConnector";
import { MessageConnectorSession } from "../net/MessageConnectorSession";
import { Message } from "../net/Message";
import { HeartbeatContext } from "./HeartbeatContext";
import { HeartbeatMachineListener } from "./HeartbeatMachineListener"
import { Protocol } from "./Protocol";

/**
 * 心跳管理机。
 * 用于定时向服务器发送心跳并处理接收到的心跳请求。
 */
export class HeartbeatMachine {
    
    /**
     * @param {MessageConnector} connector 指定连接器。
     */
    constructor(connector) {
        /**
         * 连接器。
         * @type {MessageConnector}
         */
        this.connector = connector;

        /**
         * 执行心跳任务的时间间隔，单位：毫秒。
         * @type {number}
         * @default 45000
         */
        this.hbInterval = 45000;

        /**
         * 每个心跳的有效时长，超过该时长认为对方无应答，触发心跳超时事件。单位：毫秒。
         * @type {number}
         * @default 10000
         */
        this.hbDuration = 10000;

        /**
         * 任务定时器。
         * @type {number}
         * @private
         */
        this.daemon = 0;

        /**
         * 维护的会话映射。
         * @type {FastMap}
         */
        this.contextMap = new FastMap();

        /**
         * 监听器。
         * @type {HeartbeatMachineListener}
         */
        this.listener = null;
    }

    /**
     * 启动。让管理器开始工作。
     */
    startup() {
        if (0 == this.daemon) {
            var self = this;
            this.daemon = setInterval(function() {
                self._daemon();
            }, this.hbInterval);
        }
    }

    /**
     * 关闭。管理器停止工作。
     */
    shutdown() {
        if (0 != this.daemon) {
            clearInterval(this.daemon);
            this.daemon = 0;
        }
    }

    /**
     * 设置心跳间隔。
     *
     * @param {number} interval 指定新的心跳间隔，单位：毫秒。
     */
    setHeartbeatInterval(interval) {
        if (this.hbInterval != interval) {
            this.hbInterval = interval;
            this.shutdown();
            this.startup();
        }
    }

    /**
     * 设置监听器。
     *
     * @param {HeartbeatMachineListener} listener 指定监听器。
     */
    setListener(listener) {
        this.listener = listener;
    }

    /**
     * 添加被管理的会话。
     *
     * @param {MessageConnectorSession} session 指定待管理的会话。
     * @param {string} tag 指定会话对应的标签。
     */
    addSession(session, tag) {
        if (this.contextMap.containsKey(session.getId())) {
            return;
        }

        this.contextMap.put(session.getId(), new HeartbeatContext(session, tag));
    }

    /**
     * 移除被管理的会话。
     *
     * @param {MessageConnectorSession} session 指定待移除的会话。
     */
    removeSession(session) {
        this.contextMap.remove(session.getId());
    }

    /**
     * 获取指定会话的心跳上下文。
     *
     * @param {MessageConnectorSession} session 指定会话。
     * @returns {HeartbeatContext} 返回心跳上下文。
     */
    getContext(session) {
        return this.contextMap.get(session.getId());
    }

    /**
     * 处理来自对端的心跳数据。
     *
     * @param {MessageConnectorSession} session 接收到数据的会话。
     * @param {Uint8Array} packet 接收到的数据包。
     * @param {number} time 接收到数据包时的时间。
     */
    processHeartbeat(session, packet, time) {
        if (Logger.isDebugLevel()) {
            Logger.d("HM", "Heartbeat from " + session.address.toString());
        }

        var ctx = this.contextMap.get(session.getId());
        if (null == ctx) {
            Logger.w("HM", "No context: " + session.toString());
            return;
        }

        // 解析协议
        var protocol = Protocol.deserializeHeartbeat(packet);
        // 进行应答
        var buf = Protocol.serializeHeartbeatAck(protocol.tag, protocol.originateTimestamp, time, Date.now());

        // 回送心跳应答
        var message = new Message(buf);
        this.connector.write(message);
    }

    /**
     * 处理对端返回的心跳应答。
     *
     * @param {MessageConnectorSession} session 接收到数据的会话。
     * @param {Uint8Array} packet 接收到的数据包。
     */
    processHeartbeatAck(session, packet) {
        var ctx = this.contextMap.get(session.getId());
        if (null == ctx) {
            Logger.e("HM", "Can not find session : " + session.toString());
            return;
        }

        var protocol = Protocol.deserializeHeartbeatAck(packet);
        if (protocol.originateTimestamp == ctx.originateTimestamp) {
            Logger.d("HM", "Received ack: " + session.toString());
        }
        else {
            Logger.d("HM", "Can not find context : " + session.toString());
        }

        // 记录时间
        ctx.receiveTimestamp = protocol.receiveTimestamp;
        ctx.transmitTimestamp = protocol.transmitTimestamp;
        ctx.localTimestamp = Date.now();
    }

    /**
     * 定时执行心跳的任务。
     * @private
     */
    _daemon() {
        var time = Date.now();
        var values = this.contextMap.values();

        if (Logger.isDebugLevel()) {
            Logger.d('HM', 'Heartbeat (' + values.length + ')');
        }

        for (var i = 0; i < values.length; ++i) {
            var ctx = values[i];

            // 判断心跳
            var duration = ctx.localTimestamp - ctx.originateTimestamp;
            if (duration > this.hbDuration) {
                this.listener.onSessionTimeout(ctx.session, duration);
            }

            ctx.originateTimestamp = time;
            // 设置时间
            var buf = Protocol.serializeHeartbeat(ctx.tag.toString(), time);
            var message = new Message(buf);
            this.connector.write(message);
        }
    }
}
