import { MessageHandler } from "./MessageHandler";

/**
 * 消息服务接口。
 * 
 * @interface
 */
export class MessageService {

    constructor() {
        /**
         * 处理消息的句柄。
         * @type {MessageHandler}
         */
        this.handler = null;
    }

    /**
     * 设置消息处理句柄。
     * 
     * @param {MessageHandler} handler 消息处理句柄。
     */
    setHandler(handler) {
        this.handler = handler;
    }

    /**
     * 返回消息处理句柄。
     * 
     * @returns {MessageHandler} 消息处理句柄。
     */
    getHandler() {
        return this.handler;
    }
}
