import { FastMap } from "../utils/FastMap";
import { Session } from "../net/Session";
import { Primitive } from "./Primitive";

/**
 * 管理发送的原语数据关系。
 */
export class PrimitiveCapsule {

    /**
     * @param {Session} session 会话上下文。
     */
    constructor(session) {
        this.session = session;
        this.data = new FastMap();
    }

    /**
     * 添加待管理原语。
     *
     * @param {string} cellet 指定该原语送往的 Cellet 名称。
     * @param {Primitive} primitive 指定原语。
     */
    addPrimitive(cellet, primitive) {
        var list = this.data.get(cellet);
        if (null == list) {
            this.data.put(cellet, [ primitive ]);
        }
        else {
            this.data.put(cellet, list.push(primitive));
        }
    }

    /**
     * 移除原语。
     *
     * @param {string} cellet 指定该原语送往的 Cellet 名称。
     * @param {Array} sn 指定原语 SN 。
     */
    removePrimitive(cellet, sn) {
        var result = null;

        var list = this.data.get(cellet);
        if (null != list) {
            for (var i = 0; i < list.length; ++i) {
                var primitive = list[i];
                if (primitive.equalsSN(sn)) {
                    // SN 相等，移除
                    result = primitive;
                    list.splice(i, 1);
                    break;
                }
            }
        }

        return result;
    }

    /**
     * 清空保存的数据。
     */
    clean() {
        this.data.clear();
    }
}
