import { Primitive } from "./Primitive";

/**
 * 用于跟踪消息发送的消息上下文描述。
 */
export class MessageContext {

    /**
     * @param {string} target 指定目标 Cellet 名称。
     * @param {Primitive} primitive 指定原语。
     */
    constructor(target, primitive) {
        /**
         * 目标 Cellet 名称。
         * @type {string}
         */
        this.target = target;

        /**
         * 发送的原语。
         * @type {Primitive}
         */
        this.primitive = primitive;
    }
}
