import { Utils } from "../utils/Utils";
import { Stuff } from "./Stuff";
import { LiteralBase } from "./LiteralBase";

/**
 * 原语。用于封装通讯时的数据。
 */
export class Primitive {

    /**
     * @param {Array} [sn] 原语序号。
     */
    constructor(sn) {
        /**
         * 原语的序号。
         * @type {Array}
         */
        this.sn = (sn !== undefined) ? sn : Utils.randomBytes(4);

        /**
         * 原语包含的素材列表。
         * @type {Array}
         */
        this.stuffList = [];
    }

    /**
     * 返回序号。
     *
     * @returns {Array} 返回序号。
     */
    getSN() {
        return this.sn;
    }

    /**
     * 提交语素到原语。
     *
     * @param {Stuff} stuff 指定提交的语素。
     * @returns {number} 返回语素索引位置。
     */
    commit(stuff) {
        this.stuffList.push(stuff);
        return (this.stuffList.length - 1);
    }

    /**
     * 设置指定索引处的语素。
     *
     * @param {number} index 指定索引。
     * @param {Stuff} stuff 指定语素。
     */
    setStuff(index, stuff) {
        this.stuffList[index] = stuff;
    }

    /**
     * 获取指定索引位置的语素。
     *
     * @param {number} index 指定索引。
     * @returns {Stuff} 返回指定索引处的语素。
     */
    getStuff(index) {
        return this.stuffList[index];
    }

    /**
     * 删除指定索引位置的语素。
     *
     * @param {number} index 指定索引。
     */
    removeStuff(index) {
        this.stuffList.splice(index, 1);
    }

    /**
     * 语素数量。
     *
     * @returns {number} 返回包含的语素数量。
     */
    numStuff() {
        return this.stuffList.length;
    }

    /**
     * 等于判断。
     * 
     * @param {Primitive} other 其他 Stuff 对象。
     * @returns {boolean} 如果相等返回 {@linkcode true} 。
     */
    equals(other) {
        if (null == other || other === undefined) {
            return false;
        }

        if (other.stuffList.length != this.stuffList.length) {
            return false;
        }

        if (other.sn.length != this.sn.length) {
            return false;
        }

        for (let i = 0, size = this.sn.length; i < size; ++i) {
            if (other.sn[i] != this.sn[i]) {
                return false;
            }
        }

        return true;
    }

    /**
     * 判断序号是否和当前原语的序号相同。
     *
     * @private
     * @param {Array} sn 待判断原语序列号。
     * @returns {boolean} 如果相同返回 {@linkcode true} 。
     */
    equalsSN(sn) {
        if (sn.length != this.sn.length) {
            return false;
        }

        for (let i = 0; i < sn.length; ++i) {
            if (sn[i] != this.sn[i]) {
                return false;
            }
        }

        return true;
    }

    /**
     * 复制数据并返回新的原语。
     * 
     * @returns {Primitive} 返回新的原语。
     */
    clone() {
        let clone = new Primitive(this.sn);

        for (let i = 0; i < this.stuffList.length; ++i) {
            let stuff = this.stuffList[i];
            switch (stuff.literal) {
                case LiteralBase.STRING:
                    clone.commit(new Stuff(stuff.getValueAsString(), stuff.literal));
                    break;
                case LiteralBase.INT:
                    clone.commit(new Stuff(stuff.getValueAsInt(), stuff.literal));
                    break;
                case LiteralBase.LONG:
                    clone.commit(new Stuff(stuff.getValueAsLong(), stuff.literal));
                    break;
                case LiteralBase.FLOAT:
                    clone.commit(new Stuff(stuff.getValueAsFloat(), stuff.literal));
                    break;
                case LiteralBase.DOUBLE:
                    clone.commit(new Stuff(stuff.getValueAsDouble(), stuff.literal));
                    break;
                case LiteralBase.BOOL:
                    clone.commit(new Stuff(stuff.getValueAsBool(), stuff.literal));
                    break;
                case LiteralBase.JSON:
                    clone.commit(new Stuff(stuff.getValueAsJson(), stuff.literal));
                    break;
                case LiteralBase.BIN:
                    clone.commit(new Stuff(stuff.getValue(), stuff.literal));
                    break;
                default:
                    clone.commit(new Stuff(stuff.getValue(), stuff.literal));
                    break;
            }
        }
        return clone;
    }
}
