import { Logger } from "../utils/Logger";
import { FastMap } from "../utils/FastMap";
import { InetAddress } from "../utils/InetAddress";
import { NucleusTag } from "../api/NucleusTag";
import { NucleusConfig } from "../api/NucleusConfig";
import { SpeakState } from "./SpeakState";
import { Speaker } from "./Speaker";
import { TalkListener } from "./TalkListener";
import { ListenerProxy } from "./ListenerProxy";
import { Primitive } from "./Primitive";

/**
 * Talk 服务实现。
 * Talk 服务管理所有与服务器间的数据通信。
 */
export class TalkService {
    /**
     * @param {NucleusTag} tag 内核标签。
     * @param {NucleusConfig} config 内核配置。
     */
    constructor(tag, config) {
        /**
         * 内核标签。
         * @type {NucleusTag}
         */
        this.tag = tag;

        /**
         * 内核配置。
         * @type {NucleusConfig}
         */
        this.config = config;

        /**
         * 连接地址对应的 Speaker。
         * @private
         * @type {FastMap}
         */
        this.speakers = new FastMap();

        /**
         * 请求的 Cellet 对应的 Speaker 。
         * @private
         * @type {FastMap}
         */
        this.celletSpeakerMap = new FastMap();

        /**
         * Speaker 监听器代理。
         * @private
         * @type {ListenerProxy}
         */
        this.listenerProxy = new ListenerProxy(this);

        /**
         * Cellet 对应的 Talk 监听器。
         * @private
         * @type {FastMap}
         */
        this.listeners = null;

        /**
         * 全部事件监听器映射。
         * @private
         * @type {FastMap}
         */
        this.eventListeners = null;

        /**
         * 全部事件监听器列表。
         * @private
         * @type {Array}
         */
        this.allEventListeners = null;
    }

    /**
     * 关闭服务。
     */
    hangupAll() {
        var list = this.speakers.values();
        for (var i = 0; i < list.length; ++i) {
            var speaker = list[i];
            speaker.stop();
        }

        var thr = new Thread(function() {
            this.speakers.clear();
            this.celletSpeakerMap.clear();
        }, this);
        thr.delay(10);
    }

    /**
     * 浏览器是否支持 WebSocket 。
     * 
     * @returns {boolean} 返回浏览器是否支持 WebSocket 。
     */
    isWebSocketSupported() {
        return (undefined !== window.WebSocket);
    }

    /**
     * 是否已经连接到服务器或与指定的 Cellet 连接。
     * 
     * @param {string} [cellet] 指定 Cellet 名称。
     * @returns {boolean} 如果已连接返回 {@linkcode true} ，否则返回 {@linkcode false} 。
     */
    isCalled(cellet) {
        if (undefined !== cellet) {
            let speaker = this.celletSpeakerMap.get(cellet);
            if (null == speaker) {
                return false;
            }

            return speaker.state == SpeakState.Connected;
        }
        else {
            let list = this.speakers.values();
            if (list.length == 0) {
                return false;
            }

            let speaker = list[0];
            return speaker.state == SpeakState.Connected;
        }
    }

    /**
     * 与指定地址的 Cellet 建立连接。
     * 
     * @param {InetAddress} address 指定服务器地址。
     * @param {Array} [cellets] 指定通信的 Cellet 数组。
     * @returns {Speaker} 返回 Speaker 实例。
     */
    call(address, cellets) {
        if (!this.isWebSocketSupported()) {
            Logger.i('Brower', window.navigator.userAgent);
            Logger.e('TalkService', 'This brower can NOT support WebSocket');
            return null;
        }

        var key = address.toString();
        var speaker = this.speakers.get(key);
        if (null == speaker) {
            speaker = new Speaker(this.tag, address, this.listenerProxy);
            this.speakers.put(key, speaker);
        }

        // 启动 Speaker
        if (!speaker.start()) {
            // 启动失败
            this.speakers.remove(key);
            return null;
        }

        if (undefined !== cellets) {
            for (var i = 0; i < cellets.length; ++i) {
                this.celletSpeakerMap.put(cellets[i], speaker);
            }
        }

        return speaker;
    }

    /**
     * 关闭与指定地址和端口的服务器的连接。
     * 
     * @param {InetAddress} address 指定服务器地址。
     */
    hangup(address) {
        var key = address.toString();
        var speaker = this.speakers.get(key);
        if (null == speaker) {
            return;
        }

        speaker.stop();

        let list = this.celletSpeakerMap.keySet();
        for (let i = 0; i < list.length; ++i) {
            let key = list[i];
            let spk = this.celletSpeakerMap.get(key);
            if (spk == speaker) {
                this.celletSpeakerMap.remove(key);
            }
        }
    }

    /**
     * 向已连接服务器上的 Cellet 发送数据。
     * 
     * @param {string} cellet 指定 Cellet 名称。
     * @param {Primitive} primitive 指定数据封装的原语。
     * @param {boolean} [ack] 指定是否需要服务器应答。
     * @returns {boolean} 如果当前没有建立与服务器的连接返回 {@linkcode false} 。
     */
    speak(cellet, primitive, ack) {
        var speaker = this.celletSpeakerMap.get(cellet);
        if (null == speaker) {
            var list = this.speakers.values();
            if (list.length == 0) {
                return false;
            }

            speaker = list[0];
            this.celletSpeakerMap.put(cellet, speaker);
        }

        return speaker.speak(cellet, primitive, ack);
    }

    /**
     * 设置指定 Cellet 的监听器。
     * 
     * @param {string} cellet 指定 Cellet 名称。
     * @param {TalkListener} listener 指定监听器。
     */
    setListener(cellet, listener) {
        if (null == this.listeners) {
            this.listeners = new FastMap();
        }

        if (null == listener) {
            this.listeners.remove(cellet);
        }
        else {
            this.listeners.put(cellet, listener);
        }
    }

    /**
     * 添加监听指定事件的回调函数。
     * 
     * @param {string|TalkListener} eventOrListener 指定事件名称或者监听器实例。
     * 支持的事件名称有：
     * ['onListened']{@linkcode TalkListener#onListened},
     * ['onSpoke']{@linkcode TalkListener#onSpoke},
     * ['onAck']{@linkcode TalkListener#onAck},
     * ['onSpeakTimeout']{@linkcode TalkListener#onSpeakTimeout},
     * ['onContacted']{@linkcode TalkListener#onContacted},
     * ['onQuitted']{@linkcode TalkListener#onQuitted},
     * ['onFailed']{@linkcode TalkListener#onFailed} 。
     * 指定为 {@linkcode '*'} 表示监听所有事件的函数。
     * @param {TalkServiceEventCallback} [callback] 事件的回调函数。
     * 当第一个参数是 {@linkcode TalkListener} 类型时不需要设置该参数。
     * 
     * @see {@link TalkListener}
     */
    addListener(eventOrListener, callback) {
        if ((typeof eventOrListener) === 'string') {
            if (null == this.eventListeners) {
                this.eventListeners = new FastMap();
            }

            let list = this.eventListeners.get(eventOrListener);
            if (null == list) {
                list = [ callback ];
                this.eventListeners.put(eventOrListener, list);
            }
            else {
                list.push(callback);
            }
        }
        else {
            if (null == this.allEventListeners) {
                this.allEventListeners = [ eventOrListener ];
            }
            else {
                let index = this.allEventListeners.indexOf(eventOrListener);
                if (index < 0) {
                    this.allEventListeners.push(eventOrListener);
                }
            }
        }
    }

    /**
     * 移除已监听事件的回调函数。
     * 
     * @param {string|TalkListener} eventOrListener 指定事件名称或者监听器实例。
     * 支持的事件名称有：
     * ['onListened']{@linkcode TalkListener#onListened},
     * ['onSpoke']{@linkcode TalkListener#onSpoke},
     * ['onAck']{@linkcode TalkListener#onAck},
     * ['onSpeakTimeout']{@linkcode TalkListener#onSpeakTimeout},
     * ['onContacted']{@linkcode TalkListener#onContacted},
     * ['onQuitted']{@linkcode TalkListener#onQuitted},
     * ['onFailed']{@linkcode TalkListener#onFailed} 。
     * 指定为 {@linkcode '*'} 表示监听所有事件的函数。
     * @param {TalkServiceEventCallback} [callback] 事件的回调函数。
     * 当第一个参数是 {@linkcode TalkListener} 类型时不需要设置该参数。
     * 
     * @see {@link TalkListener}
     */
    removeListener(eventOrListener, callback) {
        if ((typeof eventOrListener) === 'string') {
            if (null != this.listeners) {
                this.listeners.remove(eventOrListener);
            }

            if (null == this.eventListeners) {
                return;
            }

            let list = this.eventListeners.get(eventOrListener);
            if (null != list) {
                let index = list.indexOf(callback);
                if (index >= 0) {
                    list.splice(index, 1);
    
                    if (list.length == 0) {
                        this.eventListeners.remove(eventOrListener);
                    }
                }
            }
        }
        else {
            if (null != this.allEventListeners) {
                let index = this.allEventListeners.indexOf(eventOrListener);
                if (index >= 0) {
                    this.allEventListeners.splice(index, 1);
                }
            }
        }
    }
}

/**
 * 会话回调的事件对象。
 * 
 * @class TalkEvent
 * @property {Speaker} speaker
 * @property {string} cellet
 * @property {Primitive} primitive
 * @property {TalkError} error
 */

/**
 * 通信会话事件回调。
 * 
 * @callback TalkServiceEventCallback
 * @param {TalkEvent} event
 */
